<template>
  <!-- 学员满意度评价 -->
  <Layout class="content-data">
    <div class="content-data-card">
      <div class="from-item-400">
        <el-form
          label-width="120px"
          size="medium">
          <el-form-item label="评价时间：">
            <el-col :span="20">
              <el-date-picker
                size="small"
                v-model="time"
                :picker-options="pickerTime"
                :clearable="false"
                type="daterange"
                style="width: 400px"
                range-separator="-"
                value-format="yyyy-MM-dd"
                start-placeholder="开始日期"
                end-placeholder="结束日期"/>
              <el-button
                :disabled="loading"
                size="small"
                type="primary"
                class="search"
                @click="searchButton()">搜索</el-button>
            </el-col>
            <el-col
              :span="4"
              class="export">
              <el-button
                size="small"
                type="primary"
                @click="exportData">导出数据</el-button>
            </el-col>
          </el-form-item>

        </el-form>
      </div>
      <div class="table-basic">
        <el-table
          v-loading="loading"
          :data="tableData"
          style="width: 100%">
          <el-table-column
            label="序号"
            type="index"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="advisorName"
            label="顾问"
            min-width="80"
            align="center"/>
          <el-table-column
            prop="departName"
            label="服务中心"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="passionScore"
            label="服务热情"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="intimeScore"
            label="回复及时"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="professionScore"
            label="服务专业"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="sovleScore"
            label="问题解决"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="content"
            label="建议"
            min-width="200"
            align="center"/>
          <el-table-column
            prop="evaluateDate"
            label="评价时间"
            min-width="100"
            align="center"/>
        </el-table>
      </div>
      <el-pagination
        size="small"
        :current-page="params.pageNo"
        :total="total"
        :page-size="params.pageSize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"/>
    </div>
  </Layout>
</template>
<script>
import util from '@/common/util'
import {
  getEvaluationByUid
} from '@/api/crmPanel'

import config from '../../../config'
import { runEnv } from '../../../libs/util'
const env = runEnv()
const urls = {
  // getList: '/xinxin/i/panel/evaluation/user/list',
  // exportUrl: '/xinxin/i/panel/evaluation/user/export',
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/evaluationExport'
}
const oneDay = 24 * 60 * 60 * 1000
export default {
  components: {},
  data () {
    return {
      total: 0,
      tableData: [],
      loading: false,
      time: [],
      params: {
        startTime: '',
        endTime: '',
        userId: '',
        pageSize: 10,
        pageNo: 1
      },
      oneSearch: false,
      historyParams: {},
      pickerTime: {
        disabledDate (time) {
          return time.getTime() > new Date(new Date().getTime() - oneDay)
        }
      }
    }
  },
  watch: {
    time (val) {
      this.params.startTime = util.getTimestamp(val[0] + ' 00:00:00', 10)
      this.params.endTime = util.getTimestamp(val[1] + ' 23:59:59', 10)
      if (!this.oneSearch) {
        this.search()
        this.oneSearch = true
      }
    }
  },
  mounted () {
    this.typeWeek()
    if (this.$route.query.id) {
      this.params.userId = this.$route.query.id
    }
    // this.search()
  },
  methods: {
    handleCurrentChange (val) {
      this.params.pageNo = val
      this.historyParams.pageNo = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pageSize = val
      this.historyParams.pageSize = val
      this.search(this.historyParams)
    },
    searchButton () {
      this.params.pageNo = 1
      this.search()
    },
    async search (historyParams) {
      let params = historyParams || { ...this.params }
      this.historyParams = params
      // let { data } = await this.$axios.post(urls.getList, params)
      // this.tableData = data.list || []
      // this.total = data.total
      try {
        this.loading = true
        const data = await getEvaluationByUid(params)
        this.tableData = data.records || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    // 维度周 默认选择当周到前一天 7天内
    typeWeek () {
      let end = util.getDayTime(1)
      let start = util.getDayTime(7)
      this.time = [start.FullDate, end.FullDate]
    },
    exportData () {
      const params = Object.assign({}, this.historyParams, {
        empNo: this.$store.state.user.userInfo.id
      })
      delete params.pageNo
      delete params.pageSize
      util.download(urls.exportUrl, params, 'get')
    }
  }
}
</script>

<style scoped lang="less">
  @import '../common';
  /*.content-data {*/
    /*padding: 20px;*/
  /*}*/
  .search {
    margin-left: 20px;
  }
</style>
