<template>
  <!-- im看板 -->
  <Layout class="content-data">
    <div class="content-data-card">
      <el-tabs
        v-model="activeName"
        @tab-click="handleClick">
        <el-tab-pane
          label="IM覆盖率"
          name="cover">
          <cover v-if="name === 'cover'"/>
        </el-tab-pane>
        <el-tab-pane
          label="平均首次响应时长"
          name="reply">
          <reply v-if="name === 'reply'"/>
        </el-tab-pane>
      </el-tabs>
    </div>
  </Layout>
</template>
<script>
import cover from '@/components/dataReport/im/cover'
import reply from '@/components/dataReport/im/reply'
export default {
  name: 'dataReportImChatList',
  components: {
    cover,
    reply
  },
  data () {
    return {
      activeName: ''
    }
  },
  computed: {
    name () {
      return this.$store.state.data.showImTag
    }
  },
  watch: {
    name () {
      this.activeName = this.name
    },
    activeName (val) {
      this.reset()
    }
  },
  mounted () {
    this.activeName = this.name
  },
  destroyed () {
    this.reset()
    this.$store.commit('data/setShowImTag', 'cover')
  },
  methods: {
    reset () {
      if (this.activeName === 'cover') {
        let params = {
          cities: '',
          areas: '',
          servcenters: '',
          users: ''
        }
        this.$store.commit('data/setReplyParams', params)
      }
    },
    handleClick () {
      this.$store.commit('data/setShowImTag', this.activeName)
    }
  }
}
</script>

