<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item :label="label">
        <el-select
          size="small"
          v-model="year"
          placeholder="请选择">
          <el-option
            v-for="item in list"
            :key="item"
            :label="item"
            :value="item"/>
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import util from '@/common/util'
export default {
  props: {
    label: {
      type: String,
      default: '年份：'
    },
    paramKey: {
      type: String,
      default: 'transyear'
    }
  },
  data () {
    return {
      list: [],
      year: ''
    }
  },
  watch: {
    year (val) {
      let param = {}
      param[this.paramKey] = val
      this.$emit('getParams', param)
    }
  },
  mounted () {
    this.getNowYear()
  },
  methods: {
    getNowYear () {
      // 获取从2017年 到比现在多1年的列表
      const nowYear = util.getDayTime(0).year
      for (let i = 2017; i <= nowYear + 1; i++) {
        this.list.push(i)
      }
      this.year = nowYear
    }
  }
}
</script>

