<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item :label="label">
        <el-select
          size="small"
          v-model="type"
          placeholder="请选择">
          <el-option
            v-for="item in list"
            :key="item"
            :label="item"
            :value="item"/>
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    label: {
      type: String,
      default: '学科：'
    },
    paramKey: {
      type: String,
      default: 'subjectid'
    }
  },
  data () {
    return {
      list: [
        '全部',
        '语文',
        '数学',
        '英语',
        '物理',
        '化学',
        '生物',
        '历史',
        '政治',
        '地理',
        '综合',
        '科学',
        '编程',
        '科学实践',
        '围棋',
        '文综'
      ],
      type: ''
    }
  },
  watch: {
    type (val) {
      let type = val
      if (val === '全部') {
        type = 'all'
      }
      let params = {}
      params[this.paramKey] = type
      this.$emit('getParams', params)
    }
  },
  mounted () {
    this.type = '全部'
  },
  methods: {}
}
</script>

