<template>
  <!-- 沉睡老学员唤醒 -->
  <Layout class="content-data">
    <div class="content-data-card">
      <select-time
        :type-list="timeList"
        @getParams="getParams"/>
      <select-area
        :has-time="true"
        :end-time="params.endtime"
        @getInitList="search()"
        @getParams="getParams"/>
      <class-type @getParams="getParams"/>
      <select-year
        param-key="awakeyear"
        @getParams="getParams"/>
      <class-term @getParams="getParams"/>
      <class-subject @getParams="getParams"/>
      <div class="exprot">
        <el-button
          :disabled="loading"
          size="small"
          type="primary"
          @click="searchButton()">查询</el-button>
        <el-button
          size="small"
          type="primary"
          @click="exprot">导出数据</el-button>
      </div>
      <div class="table-basic">
        <el-table
          v-loading="loading"
          :data="tableData"
          style="width: 100%">
          <el-table-column
            type="index"
            label="序号"
            min-width="60"
            align="center"/>
          <el-table-column
            prop="queryDate"
            label="时间"
            min-width="150"
            align="center"/>
          <el-table-column
            prop="cityName"
            label="城市"
            min-width="80"
            align="center"/>
          <el-table-column
            prop="areaName"
            label="区域"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="servicecenterName"
            label="服务中心"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="userName"
            label="家庭顾问"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="sleepStudentNum"
            label="沉睡老学员人数"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="payNum"
            label="缴费人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="payManTime"
            label="缴费人次"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="netPayNum"
            label="净缴费人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="netPayManTime"
            label="净缴费人次"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="awakeRate"
            label="唤醒率"
            min-width="100"
            align="center"/>
        </el-table>
      </div>
      <el-pagination
        :current-page="params.page"
        :total="total"
        :page-size="params.pagesize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"/>
    </div>
  </Layout>
</template>
<script>
import selectArea from '../../components/dataReport/selectArea'
import selectTime from '../../components/dataReport/selectTime'
import selectYear from '../../components/dataReport/selectYear'
import classType from '../../components/dataReport/classType'
import classTerm from '../../components/dataReport/classTerm'
import classSubject from '../../components/dataReport/classSubject'
import util from '../../common/util'
import verify from './verify.js'
import { list as getList } from '@/api/crmPanel'

import config from '../../config'
import { runEnv } from '../../libs/util'
const env = runEnv()
const urls = {
  // getList: '/xinxin/i/panel/list?classify=awaken',
  // exportUrl: '/xinxin/i/panel/download?classify=awaken',
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/download'
  // exportUrl: 'https://gateway-one.speiyou.cn/SPLAT/crmPanelList/download'
}

export default {
  name: 'dataReportOldSleeping',
  components: {
    selectArea,
    selectTime,
    selectYear,
    classType,
    classTerm,
    classSubject
  },
  data () {
    return {
      total: 0,
      timeList: ['1', '2', '3', '5', '6'],
      params: {
        starttime: '',
        endtime: '',
        counttime: '1',
        cities: '',
        areas: '',
        servcenters: '',
        collect: '',
        users: '',
        serviceline: '',
        awakeyear: '',
        pagesize: 10,
        page: 1
      },
      historyParams: {},
      tableData: [],
      loading: false
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.params.page = val
      this.historyParams.page = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pagesize = val
      this.historyParams.pagesize = val
      this.search(this.historyParams)
    },
    getParams (p) {
      let params = {
        ...this.params,
        ...p
      }
      this.params = params
    },
    searchButton () {
      this.params.page = 1
      this.search()
    },
    async search (historyParams) {
      let params = historyParams || { ...this.params }
      let val = verify(params)
      if (!val.check) {
        this.$message({
          message: val.msg,
          type: 'warning'
        })
        return
      }
      params.type = params.counttime
      params.operate = 'awaken'
      this.historyParams = params
      // let { data } = await this.$axios.post(urls.getList, params)
      // this.tableData = data.records || []
      // this.total = data.totalCount
      try {
        this.loading = true
        const data = await getList(params)
        this.tableData = data.records || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    exprot () {
      util.download(urls.exportUrl, this.historyParams)
    }
  }
}
</script>

<style scoped lang ="less">
  @import './common';
</style>
