<template>
  <!-- 平均首次响应时长 -->
  <div>
    <selectJxMonth
      :init-params="storageParams"
      @getParams="getParams"/>
    <!-- <select-time
      :init-params="storageParams"
      @getParams="getParams"/> -->
    <select-area
      :has-time="true"
      :end-time="params.deadline"
      :init-params="storageParams"
      @getInitList="search()"
      @getParams="getParams"/>
    <div class="exprot">
      <el-button
        :disabled="loading"
        size="small"
        type="primary"
        @click="searchButton()">查询</el-button>
      <el-button
        size="small"
        type="primary"
        @click="exprot">导出数据</el-button>
    </div>
    <div class="table-basic">
      <el-table
        v-loading="loading"
        :data="tableData"
        style="width: 100%">
        <el-table-column
          type="index"
          label="序号"
          min-width="100"
          align="center"/>
        <el-table-column
          prop="queryDate"
          label="时间"
          min-width="130"
          align="center"/>
        <el-table-column
          prop="cityName"
          label="城市"
          min-width="100"
          align="center"/>
        <el-table-column
          prop="areaName"
          label="区域"
          min-width="120"
          align="center"/>
        <el-table-column
          prop="servicecenterName"
          label="服务中心"
          min-width="150"
          align="center"/>
        <el-table-column
          prop="userName"
          label="家庭顾问"
          min-width="120"
          align="center"/>
        <el-table-column
          prop="sendNum"
          label="推送消息条数"
          min-width="120"
          align="center"/>
        <el-table-column
          prop="recvNum"
          label="接收消息条数"
          min-width="120"
          align="center"/>
        <el-table-column
          prop="avgFirstResponseTime"
          label="平均首次响应时长"
          min-width="140"
          align="center"/>
        <el-table-column
          v-if="storageParams.collect === '4' || storageParams.collect === '5'"
          fixed="right"
          label="查看IM聊天数据"
          min-width="140"
          align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              @click="look(scope.row)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      size="small"
      :current-page="params.page"
      :total="total"
      :page-size="params.pagesize"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"/>
  </div>
</template>
<script>
import selectArea from '@/components/dataReport/selectArea'
import selectJxMonth from '@/components/dataReport/selectJxMonth'
import selectTime from '@/components/dataReport/selectTime'
import verify from '../../../view/dataReport/verify.js'
import util from '@/common/util'
import { list as getList } from '@/api/crmPanel'
import config from '../../../config'
import { runEnv } from '../../../libs/util'
const env = runEnv()
const urls = {
  // getList: '/xinxin/i/panel/list?classify=imduration',
  // exportUrl: '/xinxin/i/panel/download?classify=imduration',
  // exportUrl: 'https://gateway-one.speiyou.cn/SPLAT/crmPanelList/download'
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/download'
}

export default {
  components: {
    selectArea,
    selectJxMonth,
    selectTime
  },
  data () {
    return {
      total: 0,
      params: {
        deadline: '',
        starttime: '',
        endtime: '',
        cities: '',
        areas: '',
        servcenters: '',
        collect: '',
        users: '',
        pagesize: 10,
        page: 1
      },
      historyParams: {},
      tableData: [],
      loading: false
    }
  },
  computed: {
    storageParams () {
      return this.$store.state.data.replyParams || {}
    }
  },
  watch: {},
  mounted () {
    this.params = {
      ...this.params,
      ...this.storageParams
    }
  },
  methods: {
    look (row) {
      this.$router.push({
        name: 'dataReportImChat',
        query: {
          id: row.userId
        }
      })
    },
    handleCurrentChange (val) {
      this.params.page = val
      this.historyParams.page = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pagesize = val
      this.historyParams.pagesize = val
      this.search(this.historyParams)
    },
    searchButton () {
      this.params.page = 1
      this.search()
    },
    async search (historyParams) {
      let params = historyParams || { ...this.params }
      let val = verify(params)
      if (!val.check) {
        this.$message({
          message: val.msg,
          type: 'warning'
        })
        return
      }
      params.operate = 'imduration'
      this.historyParams = params
      this.$store.commit('data/setReplyParams', params)
      // const data = await getList(params)
      // // let { data } = await this.$axios.post(urls.getList, params)
      // this.tableData = data.records || []
      // this.total = data.totalCount
      try {
        this.loading = true
        const data = await getList(params)
        // let { data } = await this.$axios.post(urls.getList, params)
        this.tableData = data.records || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    getParams (p) {
      let params = {
        ...this.params,
        ...p
      }
      this.params = params
    },
    exprot () {
      util.download(urls.exportUrl, this.historyParams)
    }
  }
}
</script>

<style scoped lang="less">
  .exprot {
    text-align: right;
  }
</style>
