<template>
  <div class="item">
    <div class="name-num">
      <div>
        <span class="data-name">{{ keyMap.name }}</span>
        <Tooltip transfer  :content="keyMap.tooltip" max-width="200">
          <Icon type="ios-information-circle-outline"/>
        </Tooltip>
      </div>
      <div class="data-num">{{ keyMap.value | toThousands}}</div>
    </div>
    <div ref="chart" class="chart"></div>
  </div>
</template>

<script>
// 引入 echarts 核心模块，核心模块提供了 echarts 使用必须要的接口。
import * as echarts from 'echarts/core'
// 引入柱状图图表，图表后缀都为 Chart
import {
  BarChart
} from 'echarts/charts'
// 引入 Canvas 渲染器，注意引入 CanvasRenderer 或者 SVGRenderer 是必须的一步
import {
  CanvasRenderer
} from 'echarts/renderers'
// 注册必须的组件
echarts.use(
  [BarChart, CanvasRenderer]
)
export default {
  name: 'flow-item',
  props: {
    keyMap: Object,
    max: Number
  },
  watch: {
    max (val) {
      if (val >= 0) {
        this.renderChart()
      }
    },
    'keyMap.value' (val) {
      if (val >= 0) {
        this.renderChart()
      }
    }
  },
  methods: {
    renderChart () {
      this.$nextTick(() => {
        let myChart = echarts.getInstanceByDom(this.$refs.chart)
        if (myChart == null) { // 如果不存在，就进行初始化
          myChart = echarts.init(this.$refs.chart)
          window.addEventListener('resize', () => {
            myChart.resize()
          })
        }
        const option = {
          color: ['#5B8FF9', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc'],
          legend: {
            show: false,
            data: ['Direct']
          },
          grid: {
            left: 1,
            right: 1,
            top: 1,
            bottom: 1
          },
          xAxis: {
            show: false,
            max: this.max
          },
          yAxis: {
            show: false,
            type: 'category',
            data: ['Mon']
          },
          series: [{
            type: 'bar',
            stack: 'total',
            showBackground: true,
            data: [ this.keyMap.value ]
          }]
        }
        // 绘制图表
        myChart.setOption(option)
      })
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.renderChart()
    })
  }
}
</script>

<style scoped lang="less">
  .item {
    .name-num {
      display: flex;
      justify-content: space-between;
      .data-name {
        font-family: PingFang SC;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: #000000;
      }
      .data-num {
        font-family: PingFang SC;
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        text-align: right;
        color: #000000;
      }
    }
    .chart {
      height: 16px;
    }
  }
</style>
