const verify = (params, type) => {
  // type === 2 电呼
  let val = { msg: '', check: true }
  const { collect, cities, areas, servcenters, users } = params
  switch (collect) {
    case '1': // 选择城市汇总
      if (!cities) {
        val.check = false
        val.msg = '城市不能为空'
      }
      break
    case '2': // 选择区域汇总
      if (!areas) {
        val.check = false
        val.msg = '区域不能为空'
      }
      break
    case '3': // 选择服务中心汇总
      if (!servcenters) {
        val.check = false
        val.msg = '服务中心不能为空'
      }
      break
    default:
      // 选择家庭顾问汇总
      let msg = '家庭顾问不能为空'
      if (type === 2) {
        msg = '电呼坐席不能为空'
      }
      if (!users) {
        val.check = false
        val.msg = msg
      }
      break
  }
  return val
}
export default verify
