import axios from '@/libs/api.request'
/**
 * 新注册流量漏斗数据
 * @param params
 * @returns {*}
 */
export function getFunnel (params) {
  return axios.request({
    url: '/resource/funnel',
    method: 'post',
    data: params
  })
}
