<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item
        v-if="showCity"
        label="城市：">
        <select-all
          :data-list="cityList"
          v-model="city"
          :init-select="city"
          value-key="id"
          label-key="deptName"
          @change="changCity"/>
        <span
          v-if="cityList.length > 1"
          class="remarks">（可多选，默认选择全部）</span>
      </el-form-item>
      <el-form-item
        v-if="showRegion"
        label="区域：" >
        <select-all
          :data-list="regionList"
          v-model="region"
          :init-select="region"
          value-key="id"
          label-key="deptName"
          @change="changeRegion"/>
        <span
          v-if="regionList.length > 1 || cityList.length > 1"
          class="remarks">（可多选，默认选择全部）</span>
      </el-form-item>
      <el-form-item
        v-if="showService"
        label="服务中心：">
        <select-all
          :data-list="serviceList"
          v-model="service"
          :init-select="service"
          value-key="id"
          label-key="deptName"
          @change="changeService"/>
        <span
          v-if="serviceList.length > 1 || regionList.length > 1"
          class="remarks">（可多选，默认选择全部）</span>
      </el-form-item>
      <el-form-item
        v-if="showService"
        label="展示数据：" >
        <el-radio
          size="small"
          v-if="showCity"
          v-model="collect"
          label="1">城市汇总数据</el-radio>
        <el-radio
          size="small"
          v-if="showRegion"
          v-model="collect"
          label="2">区域汇总数据</el-radio>
        <el-radio
          v-model="collect"
          label="3">服务中心汇总数据</el-radio>
        <el-radio
          size="small"
          v-if="showRegion"
          v-model="collect"
          label="4">家庭顾问明细数据</el-radio>
        <el-radio
          size="small"
          v-if="roleId === 5 || roleId === 4"
          v-model="collect"
          label="5">家庭顾问明细数据</el-radio>
      </el-form-item>
      <el-form-item
        v-show="(collect === '4' || collect === '5') && showService"
        label="家庭顾问：">
        <select-all
          :data-list="usersList"
          v-model="users"
          :init-select="users"
          value-key="userId"
          label-key="userName"/>
        <span class="remarks">（可多选，默认选择全部）</span>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// 部门 1: 集团；2:事业部; 3: 中间部门; 10:全国;11: 城市; 12: 区域; 13: 服务中心  14:呼叫中心
// 角色 1 超级管理员 2 城市经理 3 区域经理 4 服务中心主管 5 家庭顾问 6 电呼主管 7 电呼坐席

// 本组件根据角色区分功能，城市经理，区域主管，服务中心主管，家庭顾问 根据角色显示不同的组件
import selectAll from './selectAll'
// import cloneDeep from 'lodash/cloneDeep'
import util from '@/common/util'
import {
  getDepartChildrenHistory as getDepartList,
  getUserByIdentityHistory as getUsers,
  getIdentityHistory,
  getDepartHistory
} from '../../api/crmPanel'
// const urls = {
//   getAllDentityUser: '/xinxin/i/identity/history',
//   getAllDentity: '/xinxin/i/identity/history/depart',
//   getDepartList: '/xinxin/i/depart/children/history',
//   getUsers: '/xinxin/i/identity/history/user'
// }
export default {
  components: {
    selectAll
  },
  props: {
    initParams: {
      type: Object,
      default: function () {
        return {
          cities: '',
          areas: '',
          servcenters: '',
          users: ''
        }
      }
    },
    startTime: {
      type: String,
      default: ''
    },
    endTime: {
      type: String,
      default: ''
    },
    hasTime: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      cityList: [],
      regionList: [],
      serviceList: [],
      city: [],
      region: [],
      service: [],
      collect: '1', // 展示数据 '1城市汇总，2区域汇总，3服务中心汇总，4家庭顾问汇总，5家庭(坐席)顾问明细'
      usersList: [],
      users: [],
      params: {},
      initGetList: true // 第一次请求列表
    }
  },
  // collect 城市 区域角色时 选中collect 家庭顾问明细值是4，服务中心主管，家庭顾问角色选中家庭顾问明细值是5
  computed: {
    userInfo () {
      return this.$store.state.roleRelativeData.userInfo
    },
    roleId () {
      return this.userInfo.crmRoleId
    },
    showCity () {
      return this.roleId === 1 || this.roleId === 2
    },
    showRegion () {
      return this.roleId === 1 || this.roleId === 2 || this.roleId === 3
    },
    showService () {
      return (
        this.roleId === 1 ||
        this.roleId === 2 ||
        this.roleId === 3 ||
        this.roleId === 4
      )
    },
    handler () {
      const { roleId, endTime, hasTime, startTime } = this
      return { roleId, endTime, hasTime, startTime }
    },
    handlerParams () {
      const { city, region, service, users } = this
      return { city, region, service, users }
    }
  },
  watch: {
    // 页面选项值变化 通知父级页面
    handlerParams (val) {
      this.emithandle()
    },
    collect (val) {
      this.emithandle()
      if (this.roleId !== 5) {
        this.getUsersList(this.service, 1)
      }
    },
    // 如果需要hasTime 开始时间 结束时间变化 要更新列表 列表和时间有关
    handler (val) {
      const { roleId, endTime, hasTime } = val
      if (!roleId) return
      if (hasTime && endTime) {
        this.init(this.userInfo)
      } else if (!hasTime) {
        this.init(this.userInfo)
      }
    }
  },
  mounted () {
    this.params = { ...this.initParams }
    if (this.roleId && !this.hasTime) {
      // this.init(this.userInfo)
      this.init()
    }
  },
  methods: {
    async init () {
      switch (this.roleId) {
        // 超级管理员
        case 1:
          this.getNewCollect('1')
          this.getAllCity()
          break
        // 城市经理
        case 2:
          this.getNewCollect('1')
          this.getMyCity()
          break
        // 区域经理
        case 3:
          this.getNewCollect('2')
          this.getRegionList([])
          break
        // 服务中心主管
        case 4:
          this.getServiceList([])
          this.getNewCollect('3')
          break
        // 家庭顾问
        case 5:
          this.collect = '5'
          this.getUsersList([])
          break
      }
    },
    // 通知父级修改参数
    emithandle () {
      let params = {
        collect: this.collect,
        cities: this.city.join(','),
        areas: this.region.join(','),
        servcenters: this.service.join(','),
        users: this.users.join(',')
      }
      // 未选中家庭顾问明细时，users置为空
      if (!(params.collect === '4' || params.collect === '5')) {
        params.users = ''
      }
      this.$emit('getParams', params)
      if (!this.initGetList) {
        return
      }
      // 家庭顾问的情况
      switch (this.roleId) {
        case 5:
          // 家庭顾问角色登录 家庭顾问有值时开始查询列表
          if (this.users.length > 0) {
            this.initGetList = false
            // 通知父级初始化查询列表
            this.$emit('getInitList')
          }
          break
        default:
          // 其他角色登录 初始化选中的是家庭顾问明细，家庭顾问有值开始查询
          if (this.collect === '4' || this.collect === '5') {
            if (this.users.length > 0) {
              this.initGetList = false
              this.$emit('getInitList')
            }
          } else {
            // 选中的不是家庭顾问明细 服务中心有值开始查询
            if (this.service.length > 0) {
              this.initGetList = false
              this.$emit('getInitList')
            }
          }
          break
      }
    },
    changCity (val) {
      this.getRegionList(val)
    },
    changeRegion (val) {
      this.getServiceList(val)
    },
    changeService (val) {
      this.getUsersList(val)
    },
    // 根据登录人的身份 获取平级的信息（如城市经理，获取时间范围内 所在过的城市）
    async getAllDentity () {
      let params = {
        startTime: util.getTimestamp(this.startTime, 10) || 0,
        endTime: util.getTimestamp(this.endTime, 10) || 0
      }
      // let { data } = await this.$axios.post(urls.getAllDentity, params)
      try {
        const data = await getDepartHistory(params)
        return data || []
      } catch (err) {
        this.$Message.error({
          content: '获取身份对应信息失败：' + err.message
        })
        return []
      }
    },
    // 根据上级deptPid获取下级部门
    async getDepart (val) {
      if (val && val.length < 1) {
        return []
      }
      let params = {
        startTime: util.getTimestamp(this.startTime, 10) || 0,
        endTime: util.getTimestamp(this.endTime, 10) || 0,
        deptPids: val
      }
      // let { data } = await this.$axios.post(urls.getDepartList, params)
      // const data = await getDepartList(params)
      // return data || []
      try {
        const data = await getDepartList(params)
        return data || []
      } catch (err) {
        this.$Message.error({
          content: '根据上级deptPid获取下级部门失败：' + err.message
        })
        return []
      }
    },
    // 按服务中心查询家庭顾问
    async getUsers (val) {
      let searchIds = [...val]
      if (!(this.collect === '4' || this.collect === '5') || val.length <= 0) {
        return []
      }
      let params = {
        startTime: util.getTimestamp(this.startTime, 10) || 0,
        endTime: util.getTimestamp(this.endTime, 10) || 0,
        deptId: searchIds.join(',')
      }
      // let { data } = await this.$axios.post(urls.getUsers, params)
      // const data = await getUsers(params)
      // return data || []
      try {
        const data = await getUsers(params)
        return data || []
      } catch (err) {
        this.$Message.error({
          content: '按服务中心查询家庭顾问失败：' + err.message
        })
        return []
      }
    },
    // 初始化家庭顾问，家庭顾问身份登录，获取时间范围内的 家庭顾问
    async getUsersInit () {
      if (!(this.collect === '4' || this.collect === '5')) {
        return []
      }
      let params = {
        startTime: util.getTimestamp(this.startTime, 10) || 0,
        endTime: util.getTimestamp(this.endTime, 10) || 0
      }
      // let { data } = await this.$axios.post(urls.getAllDentityUser, params)
      // let data = await getIdentityHistory(params)
      // 顾问身份登录，查询不到历史顾问时， 默认用登录人顾问信息
      // if (data && data.length <= 0) {
      //   data = [
      //     { userId: this.userInfo.userId, userName: this.userInfo.userName }
      //   ]
      // }
      // return data || []

      try {
        let data = await getIdentityHistory(params)
        if (data && data.length <= 0) {
          data = [
            { userId: this.userInfo.userId, userName: this.userInfo.userName }
          ]
        }
        return data || []
      } catch (err) {
        this.$Message.error({
          content: '初始化家庭顾问失败：' + err.message
        })
        return []
      }
    },
    // 超级管理员查询全部城市
    async getAllCity () {
      // 超级管理员查城市 是11
      this.cityList = await this.getDepart(['10'])
      let ids = this.cityList.map(item => item.id)
      // 返回初始化选中的值
      if (this.params.cities.length > 0) {
        // 把已逗号分隔的字符串改为数组 并且遍历string转int
        this.city = this.strToIntArray(this.initParams.cities)
        this.params.cities = ''
      } else {
        // 默认全选
        this.city = ids
      }
      this.getRegionList(this.city)
    },

    // 城市经理获取
    async getMyCity () {
      this.cityList = await this.getAllDentity()
      let ids = this.cityList.map(item => item.id)
      // 返回初始化选中的值
      if (this.params.cities.length > 0) {
        // 把已逗号分隔的字符串改为数组 并且遍历string转int
        this.city = this.strToIntArray(this.initParams.cities)
        this.params.cities = ''
      } else {
        // 默认全选
        this.city = ids
      }
      this.getRegionList(this.city)
    },
    // 获取区域
    async getRegionList (val) {
      // 全国做特殊处理，全国的下级是服务中心 把全国放到区域去处理
      let searchIds = [...val]
      if (val.includes(12)) {
        let index = searchIds.indexOf(12)
        searchIds.splice(index, 1)
      }
      // 角色为区域经理时 默认查询所有的区域
      this.regionList =
        this.roleId === 3
          ? await this.getAllDentity()
          : await this.getDepart(searchIds)
      let ids = this.regionList.map(item => item.id)
      // 返回初始化选中的值
      if (this.params.areas.length > 0) {
        this.region = this.strToIntArray(this.initParams.areas)
        this.params.areas = ''
      } else {
        // 默认全选
        this.region = ids
      }
      this.getServiceList(this.region)
    },
    // 获取服务中心
    async getServiceList (val) {
      // 全国做特殊处理，全国的下级是服务中心 把全国放到区域去处理
      let searchIds = [...val]
      if (this.city.includes(12)) {
        searchIds = [12].concat(searchIds)
      }
      // 角色为服务中心主管时 默认查询用户所有的服务中心
      this.serviceList =
        this.roleId === 4
          ? await this.getAllDentity()
          : await this.getDepart(searchIds)
      let ids = this.serviceList.map(item => item.id)

      this.service = ids
      // 返回初始化选中的值
      if (this.params.servcenters.length > 0) {
        this.service = this.strToIntArray(this.initParams.servcenters)
        this.params.servcenters = ''
      } else {
        // 默认全选
        this.service = ids
      }
      this.getUsersList(this.service)
    },
    // 选择家庭顾问查询
    async getUsersList (val, type) {
      // type =1 是切换家庭顾问选项时 不算第一次请求
      let usersList =
        this.roleId === 5 ? await this.getUsersInit() : await this.getUsers(val)
      let idObj = {}
      let newList = []
      usersList.forEach(item => {
        if (!idObj[item.userId]) {
          idObj[item.userId] = true
          newList.push(item)
        }
      })
      this.usersList = [...newList]
      let ids = this.usersList.map(item => item.userId)
      // 返回初始化选中的值
      if (this.params.users.length > 0 && (type !== 1 || this.roleId === 4)) {
        this.users = this.strToIntArray(this.params.users)
        this.params.users = ''
      } else {
        // 默认全选
        this.users = ids
      }
    },
    // 选中展示数据
    getNewCollect (val) {
      if (this.params.collect) {
        this.collect = this.initParams.collect
        this.params.collect = ''
      } else {
        this.collect = val
      }
    },
    // 把已逗号分隔的字符串改为数组 并且遍历string转int
    strToIntArray (val) {
      let list = val.split(',')
      list.forEach((item, index) => {
        list[index] = parseInt(item)
      })
      return list
    }
  }
}
</script>

