import { getDeptByAreaCode, getIdentityHistoryByDate } from '@/api/crm'
import { queryPerformanceByCondition } from '@/api/user'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      performanceType: 0,
      dateRange: [],
      scList: [],
      scIds: [],
      userList: [],
      userIds: [],
      collectType: '',
      tableData: [],
      pageNo: 1,
      pageSize: 50,
      pageSizes: [ 25, 50, 100, 200 ],
      totalCount: 0,
      loading: false
    }
  },
  methods: {
    querySCList () {
      getDeptByAreaCode({
        areaCode: this.cityCode
      }).then(data => {
        this.scList = data
        this.scIds = data.map(d => d.deptCode)
      }).catch(err => {
        this.$message.error('查询服务中心列表错误：' + err.message)
      })
    },
    queryUserList (dateRange) {
      if (dateRange.length > 0) {
        getIdentityHistoryByDate({
          areaCode: this.cityCode,
          startTime: dateRange[0],
          endTime: dateRange[1],
          roleId: this.roleId,
          deptId: this.deptId
        }).then(data => {
          this.userList = data
          this.userIds = data.map(d => d.identityId)
        }).catch(err => {
          this.$message.error('查询顾问列表错误：' + err.message)
        })
      }
    },
    search (from) {
      if (!this.dateRange.length) {
        this.$message.warning('请选择日期')
        return
      }
      if (this.collectType === '4' && !this.scIds.length) {
        this.$message.warning('请选择服务中心')
        return
      }
      if (this.collectType === '5' && !this.userIds.length) {
        this.$message.warning('请选择家庭顾问')
        return
      }
      if (from === 1) {
        this.pageNo = 1
      }
      this.loading = true
      queryPerformanceByCondition({
        areaCodeList: [ this.cityCode ],
        startDate: this.dateRange[0],
        endDate: this.dateRange[1],
        performanceQueryType: this.collectType,
        baseDataIdList: this.scIds,
        identityIdList: this.userIds,
        pageNo: this.pageNo,
        pageSize: this.pageSize,
        performanceType: this.performanceType
      }).then(data => {
        this.tableData = data.records
        this.totalCount = data.totalCount
      }).catch(err => {
        this.$message.error('查询绩效数据错误：' + err.message)
      }).finally(() => {
        this.loading = false
      })
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.pageNo = 1
      this.search()
    },
    handleCurrentChange (val) {
      this.pageNo = val
      this.search()
    },
    init () {
      this.collectType = String(this.roleId)
      if (this.baseDataId) {
        this.scIds = [ this.baseDataId ]
      }
    }
  },
  watch: {
    roleId () {
      this.init()
    },
    collectType (val) {
      this.tableData = []
      this.pageNo = 1
      this.totalCount = 0
      if (this.roleId === 2 && val === '4') {
        if (!this.scList.length) {
          this.querySCList()
        }
      }
    }
  },
  computed: {
    ...mapState({
      roleId: state => state.roleRelativeData.userInfo.crmRoleId,
      cityCode: state => state.roleRelativeData.userInfo.cityCode,
      baseDataId: state => state.roleRelativeData.userInfo.baseDataId,
      deptId: state => state.roleRelativeData.userInfo.crmDeptId
    })
  },
  mounted () {
    this.$nextTick(() => {
      if (this.roleId) {
        this.init()
      }
    })
  }
}
