import SelectAll from '_c/dataReport/selectAll'
import {
  queryPerformanceMonth,
  queryGroupList,
  saveAdvisorGroup,
  queryPerformDailyDetail,
  queryPerformRefundDetail,
  queryPerformanceDetailId,
  queryRefundPerformanceDataPath,
  exportById,
  queryPerformanceConsult
} from '@/api/user'

// 基础类接口 数据
import {
  getCityList,
  queryTermListForBaseData,
  getGradeList
} from '@/api/bd'

import Cookie from 'js-cookie'
import { mapState } from 'vuex'
export default {
  components: {
    SelectAll
  },
  computed: {
    // 已选顾问
    advisoryGroupChecked () {
      return this.advisoryList.filter(item => {
        return this.advisoryEmpNosChecked.indexOf(item.empNo) > -1
      })
    },
    // 顾问组字符串
    advisoryGroupString () {
      return this.advisoryGroup.reduce((p, c) => p + c.empName + ', ', '').slice(0, -1)
    },
    ...mapState({
      roleId: state => state.roleRelativeData.userInfo.crmRoleId,
      cityCode: state => state.roleRelativeData.userInfo.cityCode,
      deptId: state => state.roleRelativeData.userInfo.crmDeptId
    })
  },
  data () {
    return {
      dateSection: [],
      formData: {
        date: '',
        useAdvisoryGroup: false,
        pageNo: 1,
        pageSize: 50,
        pageSizes: [25, 50, 100, 200],
        totalCount: 0,
        stuLoginName: '', // 学员编号
        stuName: '' // 学员姓名
      },
      advisoryList: [], // 顾问列表
      advisoryGroup: [], // 顾问组
      advisoryEmpNosChecked: [], // 已选顾问编号
      tableData: [],
      monthList: [],
      loading: false,
      exportIng: false, // 导出中
      allCityList: [], // 所有分校
      gradeList: [], // 所有年级
      termList: [], // 所有学期
      areaCodeList: [], // 已选择分校
      claGradeIdList: [], // 已选择年级
      claTermIdList: [] // 已选择学期

    }
  },
  methods: {
    // 年级
    async getGradeList () {
      try {
        const params = {
          cityCode: '010'
        }
        const data = await getGradeList(params)
        this.gradeList = Object.freeze(data)
        this.claGradeIdList = data.map(item => {
          item.name = item.gradeTypeName + '-' + item.name
          return item.schoolId
        })
      } catch (error) {
        this.$Message.warning({
          content: '获取年级失败:' + error.message,
          duration: 5
        })
      }
    },
    // 获取学期
    getTermList () {
      queryTermListForBaseData({ cityCode: '010' })
        .then(data => {
          this.termList = data.slice(0, 4)
          this.claTermIdList = this.termList.map(item => item.schoolId)
        })
        .catch(err => {
          this.$Message.error('获取学期失败:' + err.message)
        })
    },
    // 获取所有城市
    getCityListHandler () {
      getCityList().then((data) => {
        this.allCityList = data
        this.areaCodeList = data.map(item => item.id)
      }).catch((err) => {
        this.$Message.warning({ content: '获取所有城市失败:' + err.message, duration: 5 })
      })
    },
    // 绩效规则下载
    downloadExcel () {
      window.open('https://speiyou-tbe-fe.oss-cn-beijing.aliyuncs.com/绩效规则.xlsx')
    },
    // 更新顾问组
    async updateAdvisoryGroup (type) {
      let newAdvisoryGroup = []
      newAdvisoryGroup = [...this.advisoryGroupChecked]
      this.formData.useAdvisoryGroup = true
      if (type === 'delete') {
        newAdvisoryGroup = []
        this.formData.useAdvisoryGroup = false
      }

      await this.saveAdvisorGroup(newAdvisoryGroup, type)
      this.queryGroupList()
    },
    // 导出
    exportData () {
      if (!this.formData.date) {
        return this.$message.warning({ message: '请选择月份' })
      }
      let empNoList = this.advisoryEmpNosChecked
      if (this.formData.useAdvisoryGroup) {
        empNoList = this.advisoryGroup.map(d => d.empNo)
      }

      let performanceQueryType = null
      switch (this.roleId) {
        case 2:
        case 4:
          performanceQueryType = 2; break
        case 5:
          empNoList = [JSON.parse(Cookie.get('columbusUserInfo')).id]
          performanceQueryType = 5; break
        case 1:
          empNoList = undefined
          performanceQueryType = 1; break
      }

      const params = {
        isLongTerm: this.isLongTerm, // 1 长期班 0短期班
        areaCodeList: this.roleId === 1 ? this.areaCodeList : [this.cityCode],
        jxMonth: this.formData.date, // 绩效月份
        jxMonthStart: this.formData.date[0],
        jxMonthEnd: this.formData.date[1],
        pageNo: this.formData.pageNo,
        pageSize: this.formData.pageSize,
        stuLoginName: this.formData.stuLoginName, // 学员编号
        stuName: this.formData.stuName, // 学员姓名
        performanceQueryType, // 查询类型
        claGradeIdList: this.isLongTerm === 1 ? this.claGradeIdList : undefined,
        claTermIdList: this.isLongTerm === 1 ? this.claTermIdList : undefined,
        empNoList // 顾问列表
      }

      this.exportIng = true
      this.$message.info({ message: '正在导出excel，请勿关闭当前标签页', duration: 5000 })
      this.queryExportId(params).then(res => {
        (function getFilePath (id, that) {
          exportById({ id, queryId: that.queryId }).then(data => {
            if (data.filePath) {
              window.open(data.filePath)
              that.exportIng = false
            } else {
              setTimeout(() => {
                getFilePath(id, that)
              }, 2000)
            }
          }).catch(err => {
            that.exportIng = false
            that.$message.error({ message: err.message, duration: 5000 })
          })
        })(res.id, this)
      }).catch(err => {
        this.exportIng = false
        this.$message.error({ message: err.message, duration: 5000 })
      })
    },
    initView () {
      if (this.roleId === 2 || this.roleId === 4) {
        this.queryAdvisoryList()
        this.queryGroupList()
      }
      if (this.source === 'detail') {
        this.queryPerformanceMonth()
      }
      if (this.roleId === 1) {
        this.getCityListHandler()
      }

      this.getGradeList()
      this.getTermList()
    },
    // 顾问列表
    queryAdvisoryList () {
      queryPerformanceConsult({
        areaCode: this.cityCode,
        roleId: this.roleId,
        deptId: this.deptId
      }).then(data => {
        this.advisoryList = data
        this.advisoryEmpNosChecked = data.map(d => d.empNo)
      }).catch(err => {
        this.$message.error('查询顾问列表失败：' + err.message)
      })
    },
    // 保存顾问组
    saveAdvisorGroup (newAdvisoryGroup, type) {
      const params = newAdvisoryGroup
      return saveAdvisorGroup(params).then(() => {
        if (type === 'delete') {
          this.$message.success({ message: '删除成功' })
        } else {
          this.$message.success({ message: '保存成功' })
        }
      }).catch(err => {
        if (type === 'delete') {
          this.$message.error({ message: '删除失败请重试' + err.message })
        } else {
          this.$message.error({ message: '保存失败请重试' + err.message })
        }
      })
    },
    // 获取顾问组
    queryGroupList () {
      const params = {
        empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
      }
      queryGroupList(params).then(res => {
        this.advisoryGroup = Object.freeze(res)
      }).catch(err => {
        this.$message.error({ message: err.message })
      })
    },
    // 获取月份
    queryPerformanceMonth () {
      const params = {
        empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
      }
      queryPerformanceMonth(params).then(res => {
        this.monthList = Object.freeze(res)
      }).catch(err => {
        this.$message.error({ message: err.message })
      })
    },
    selectChange (month) {
      this.queryAdvisoryList(month)
    },
    handleSizeChange (val) {
      this.formData.pageSize = val
      this.formData.pageNo = 1
      this.search()
    },
    handleCurrentChange (val) {
      this.formData.pageNo = val
      this.search()
    },
    // 查询
    search () {
      if (!this.formData.date && this.formData.date.length === 0) {
        return this.$message.warning({ message: '请选择月份' })
      }
      let empNoList = this.advisoryEmpNosChecked
      if (this.formData.useAdvisoryGroup) {
        empNoList = this.advisoryGroup.map(d => d.empNo)
      }

      let performanceQueryType = null
      switch (this.roleId) {
        case 2:
        case 4:
          performanceQueryType = 2; break
        case 5:
          empNoList = [JSON.parse(Cookie.get('columbusUserInfo')).id]
          performanceQueryType = 5; break
        case 1:
          empNoList = undefined
          performanceQueryType = 1; break
      }

      const params = {
        isLongTerm: this.isLongTerm, // 1 长期班 0短期班
        areaCodeList: this.roleId === 1 ? this.areaCodeList : [this.cityCode],
        jxMonth: this.formData.date, // 绩效月份
        jxMonthStart: this.formData.date[0],
        jxMonthEnd: this.formData.date[1],
        pageNo: this.formData.pageNo,
        pageSize: this.formData.pageSize,
        stuLoginName: this.formData.stuLoginName, // 学员编号
        stuName: this.formData.stuName, // 学员姓名
        performanceQueryType, // 查询类型
        claGradeIdList: this.isLongTerm === 1 ? this.claGradeIdList : undefined,
        claTermIdList: this.isLongTerm === 1 ? this.claTermIdList : undefined,
        empNoList// 顾问列表
      }

      this.loading = true
      this.query(params).then(res => {
        this.tableData = Object.freeze(res.records)
        this.formData.totalCount = res.totalCount
        this.loading = false
      }).catch(err => {
        this.loading = false
        this.$message.error({ message: err.message })
      })
    }
  },
  watch: {
    roleId () {
      this.initView()
    }
  },
  mounted () {
    if (this.source === 'detail') {
      this.query = queryPerformDailyDetail
      this.queryExportId = queryPerformanceDetailId
    } else if (this.source === 'refund') {
      this.query = queryPerformRefundDetail
      this.queryExportId = queryRefundPerformanceDataPath
    }
    this.$nextTick(() => {
      if (this.roleId) {
        this.initView()
      }
    })
  }
}
