<template>
  <el-form
    label-width="120px"
    size="medium">
    <el-form-item label="统计时间：">
      <el-radio-group v-model="type">
        <el-radio
          size="small"
          v-for="item in time"
          v-show="typeList.includes(item.value)"
          :key="item.value"
          :label="item.value"
          @change="changeTimeType">{{ item.name }}</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item
      v-show="type==='1'"
      label=" ">
      <el-date-picker
        size="small"
        v-model="time2"
        :picker-options="pickerTime"
        :clearable="false"
        type="date"
        style="width: 400px"
        value-format="yyyy-MM-dd"
        placeholder="选择日期"/>
    </el-form-item>
    <el-form-item
      v-show="type!=='1'"
      label=" ">
      <el-date-picker
        size="small"
        v-model="time1"
        :picker-options="pickerTime"
        :clearable="false"
        type="daterange"
        style="width: 400px"
        range-separator="-"
        value-format="yyyy-MM-dd"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        @change="changeTime"/>
    </el-form-item>
  </el-form>
</template>
<script>
import util from '@/common/util'
const oneDay = 24 * 60 * 60 * 1000
export default {
  props: {
    initParams: {
      type: Object,
      default: function () {
        return {}
      }
    },
    typeList: {
      type: Array,
      default: function () {
        return ['1', '2', '3', '4', '5']
      }
    }
  },
  data () {
    return {
      time: [
        { name: '按天', value: '1' },
        { name: '按自然周', value: '2' },
        { name: '按自然月', value: '3' },
        { name: '按绩效月', value: '4' },
        { name: '按季度', value: '5' },
        { name: '按年份', value: '6' }
      ],
      type: '1',
      time1: [],
      time2: '',
      lastDay: '',
      pickerTime: {
        disabledDate (time) {
          return time.getTime() > new Date(new Date().getTime() - oneDay)
        }
      }
    }
  },
  computed: {
    handleParams () {
      const { time1, time2, type } = this
      return { time1, time2, type }
    }
  },
  watch: {
    handleParams () {
      let params = {
        counttime: this.type
      }
      if (this.type === '1') {
        params.starttime = this.time2 + ' 00:00:00'
        params.endtime = this.time2 + ' 23:59:59'
      } else {
        params.starttime = this.time1[0] + ' 00:00:00'
        params.endtime = this.time1[1] + ' 23:59:59'
      }
      this.$emit('getParams', params)
    }
  },
  mounted () {
    // 三级页面返回有初始值
    if (this.initParams.counttime) {
      this.type = this.initParams.counttime
      const start = util.formatTime(
        new Date(this.initParams.starttime).getTime()
      ).FullDate
      const end = util.formatTime(new Date(this.initParams.endtime).getTime())
        .FullDate
      if (this.type === '1') {
        this.time2 = start
      } else {
        this.time1 = [start, end]
      }
    } else {
      this.typeDay()
      this.lastDay = util.getDayTime(1)
    }
  },
  methods: {
    changeTime (val) {
      let start = new Date(val[0] + ' 00:00:00')
      let end = new Date(val[1] + ' 23:59:59')
      switch (this.type) {
        case '2':
          this.getWeekTime(start, end)
          break
        case '3':
          this.getMonthTime(start, end)
          break
        case '4':
          this.getJxMonthTime(start, end)
          break
        case '5':
          this.getSeasontime(start, end)
          break
        case '6':
          this.getYearTime(start, end)
          break
      }
    },
    changeTimeType (type) {
      switch (type) {
        case '1':
          this.typeDay()
          break
        case '2':
          this.typeWeek()
          break
        case '3':
          this.typeMonth()
          break
        case '4':
          this.typeJxMonth()
          break
        case '5':
          this.typeSeason()
          break
        case '6':
          this.typeYear()
          break
      }
    },
    // 按天 时间默认为前一天
    typeDay () {
      const day = util.getDayTime(1)
      this.time2 = day.FullDate
    },
    // 维度周 默认选择当周到前一天
    typeWeek () {
      let end = util.getDayTime(1)
      let start = util.getDayTime(end.week)
      this.time1 = [start.FullDate, end.FullDate]
    },
    // 按月 默认选择当自然月到前一天
    typeMonth () {
      let end = util.getDayTime(1)
      let start = end.year + '-' + end.month + '-01'
      start = util.formatTime(new Date(start).getTime()).FullDate
      this.time1 = [start, end.FullDate]
    },
    // 按绩效月 默认选择当绩效月到一天 19号为分隔 一个绩效月是上个月的20号到这个月的19号
    typeJxMonth () {
      let end = util.getDayTime(1)
      let start = ''
      let startYear = ''
      let startMonth = ''
      if (end.day >= 20) {
        start = end.year + '-' + end.month + '-20'
      } else {
        startYear = end.month === 1 ? end.year - 1 : end.year
        startMonth = end.month === 1 ? 12 : end.month - 1
        start = startYear + '-' + startMonth + '-20'
      }
      start = util.formatTime(new Date(start).getTime()).FullDate
      this.time1 = [start, end.FullDate]
    },
    // 按季度 默认选择当季度到前一天 第一季度12-2 3-5 6-8 9-11
    typeSeason () {
      let end = util.getDayTime(1)
      let start = ''
      let startMonth = ''
      if (end.month <= 3) {
        startMonth = 1
      } else if (end.month > 3 && end.month <= 6) {
        startMonth = 4
      } else if (end.month > 6 && end.month <= 9) {
        startMonth = 7
      } else {
        startMonth = 10
      }
      start = end.year + '-' + startMonth + '-' + '01'
      start = util.formatTime(new Date(start).getTime()).FullDate
      this.time1 = [start, end.FullDate]
    },
    typeYear () {
      const day = util.getDayTime(1)
      let start = day.year + '-01-01'
      this.time1 = [start, day.FullDate]
    },
    // 工具方法，获取开始日期本周一日期 结束时间本周日 日期
    getWeekTime (start, end) {
      let startWeek = start.getDay() === 0 ? 7 : start.getDay()
      let endWeek = end.getDay() === 0 ? 7 : end.getDay()
      let time = []
      time[0] = util.formatTime(
        start.getTime() - oneDay * (startWeek - 1)
      ).FullDate
      time[1] = util.formatTime(end.getTime() + oneDay * (7 - endWeek)).FullDate
      this.time1 = time
    },
    // 工具方法，获取开始日期本月初一日期 结束时间本月最后一天
    getMonthTime (start, end) {
      let startObj = util.formatTime(start.getTime())
      let endObj = util.formatTime(end.getTime())
      let time = []
      time[0] = new Date(startObj.year, startObj.month - 1, 1)
      time[1] = new Date(endObj.year, endObj.month, 0)
      let firstDay = util.formatTime(new Date(time[0]).getTime())
      let lastDay = util.formatTime(new Date(time[1]).getTime())
      this.time1 = [firstDay.FullDate, lastDay.FullDate]
    },
    // 工具方法，获取开始日期本绩效月开始日期20号 结束时间本绩效月最后一天19号
    getJxMonthTime (start, end) {
      let startObj = util.formatTime(start.getTime())
      let endObj = util.formatTime(end.getTime())
      let time = []
      let startYear = ''
      let startMonth = ''
      let endYear = ''
      let endMonth = ''
      if (startObj.day >= 20) {
        time[0] = startObj.year + '-' + startObj.month + '-20'
      } else {
        startYear = startObj.month === 1 ? startObj.year - 1 : startObj.year
        startMonth = startObj.month === 1 ? 12 : startObj.month - 1
        time[0] = startYear + '-' + startMonth + '-20'
      }
      if (endObj.day < 20) {
        time[1] = endObj.year + '-' + endObj.month + '-19'
      } else {
        endYear = endObj.month === 12 ? endObj.year + 1 : endObj.year
        endMonth = endObj.month === 12 ? 1 : endObj.month + 1
        time[1] = endYear + '-' + endMonth + '-19'
      }
      let firstDay = util.formatTime(new Date(time[0]).getTime())
      let lastDay = util.formatTime(new Date(time[1]).getTime())
      this.time1 = [firstDay.FullDate, lastDay.FullDate]
    },
    // 工具方法，获取开始日期本季度第一天 结束时间本季度最后一天
    getSeasontime (start, end) {
      let startObj = util.formatTime(start.getTime())
      let endObj = util.formatTime(end.getTime())
      let time = []
      let startMonth = ''
      let endMonth = ''
      if (startObj.month <= 3) {
        startMonth = 1
      } else if (startObj.month > 3 && startObj.month <= 6) {
        startMonth = 4
      } else if (startObj.month > 6 && startObj.month <= 9) {
        startMonth = 7
      } else {
        startMonth = 10
      }
      if (endObj.month <= 3) {
        endMonth = 3
      } else if (endObj.month > 3 && endObj.month <= 6) {
        endMonth = 6
      } else if (endObj.month > 6 && endObj.month <= 9) {
        endMonth = 9
      } else {
        endMonth = 12
      }
      time[0] = startObj.year + '-' + startMonth + '-' + '01'
      time[1] = new Date(endObj.year, endMonth, 0)
      let firstDay = util.formatTime(new Date(time[0]).getTime())
      let lastDay = util.formatTime(new Date(time[1]).getTime())
      this.time1 = [firstDay.FullDate, lastDay.FullDate]
    },
    getYearTime (start, end) {
      let startObj = util.formatTime(start.getTime())
      let endObj = util.formatTime(end.getTime())
      let time = []
      let nowYear = util.getDayTime(1).year
      time[0] = startObj.year + '-01-01'
      if (endObj.year >= nowYear) {
        time[1] = util.getDayTime(1).FullDate
      } else {
        time[1] = endObj.year + '-12-31'
      }
      this.time1 = time
    }
    // 格式化时间戳
  }
}
</script>

