<template>
  <div ref="flowPoll" class="flow-poll">
    <div class="title-wrapper">
      <span class="title1">流量池</span>
      <Tooltip transfer  content="所筛选的时间段内，产生的资源数据统计" max-width="200">
        <Icon type="ios-information-circle-outline" :size="16"/>
      </Tooltip>
    </div>
    <div class="data-wrapper">
      <div>
        <div class="data-num">{{ flowData.totalFlow | toThousands}}</div>
        <div>
          <span class="data-name">总流量</span>
          <Tooltip transfer  content="所筛选的时间段内，加微用户、注册绑定用户去重后总量" max-width="200">
            <Icon type="ios-information-circle-outline"/>
          </Tooltip>
        </div>
      </div>
      <div>
        <div class="data-num">{{ flowData.addWxFlow | toThousands}}</div>
        <div>
          <span class="data-name">加微总量</span>
          <Tooltip transfer  content="所筛选的时间段内，加微用户量" max-width="200">
            <Icon type="ios-information-circle-outline"/>
          </Tooltip>
        </div>
      </div>
      <div>
        <div class="data-num">{{ flowData.newRegFlow | toThousands}}</div>
        <div>
          <span class="data-name">新注册总量</span>
          <Tooltip transfer content="所筛选的时间段内，注册用户量" max-width="200">
            <Icon type="ios-information-circle-outline"/>
          </Tooltip>
        </div>
      </div>
    </div>
    <div class="chart-wrapper">
      <FlowItem v-for="item in keyValueList" :key="item.key + item.value" :key-map="item" :max="max"></FlowItem>
    </div>
    <div class="compare-wrapper">
      <div :class="fontSize+' compare-item'" v-if="flowData.monthCompared">
        <div class="compare-text">月环比</div>
        <div class="data-icon" :class="flowData.monthCompared.lastIndexOf('-') === -1 ? 'up':'down'"></div>
        <div class="data-mum-text" :class="flowData.monthCompared.lastIndexOf('-') === -1 ? 'up':'down'">{{ flowData.monthCompared }}</div>
      </div>
      <div :class="fontSize+' compare-item'" v-if="flowData.dayCompared">
        <div class="compare-text">日环比</div>
        <div class="data-icon" :class="flowData.dayCompared.lastIndexOf('-') === -1 ? 'up':'down'"></div>
        <div class="data-mum-text" :class="flowData.dayCompared.lastIndexOf('-') === -1 ? 'up':'down'">{{ flowData.dayCompared }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { flowPool } from '@/api/resource'
import FlowItem from './flow-item'
export default {
  name: 'FlowPoll',
  props: {
    queryParams: Object
  },
  components: {
    FlowItem
  },
  data () {
    return {
      flowData: {},
      keyMap: [
        {
          name: '加微',
          key: 'addWx',
          tooltip: '所筛选的时间段内，加微用户量'
        },
        {
          name: '注册',
          key: 'reg',
          tooltip: '所筛选的时间段内，注册用户量'
        },
        {
          name: '有效加微',
          key: 'validAddWx',
          tooltip: '所筛选的时间段内，加微且备注手机号的用户量'
        },
        {
          name: '注册且加微',
          key: 'regAndAddWx',
          tooltip: '所筛选的时间段内，加微的注册用户量'
        },
        {
          name: '注册有效加微',
          key: 'validRegAndAddWx',
          tooltip: '所筛选的时间段内，加微且备注手机号的注册用户量'
        }
      ],
      fontSize: 'font16'
    }
  },
  mounted () {
    let _this = this
    window.addEventListener('resize', function () {
      _this.$nextTick(() => {
        _this.fontSize = _this.$refs.flowPoll.clientWidth > 310 ? 'font16' : 'font14'
      })
    })
    this.$nextTick(() => {
      this.fontSize = this.$refs.flowPoll.clientWidth > 310 ? 'font16' : 'font14'
    })
  },
  computed: {
    keyValueList () {
      return this.keyMap.map(k => {
        k.value = this.flowData[k.key]
        return k
      }).sort((a, b) => b.value - a.value)
    },
    max () {
      return Math.max(...this.keyValueList.map(kv => kv.value))
    }
  },
  methods: {
    initView () {
      flowPool(this.queryParams).then(data => {
        this.flowData = data
        this.$emit('theFlowData', this.flowData)
      }).catch(err => {
        this.$Message.error({ content: '查询流量池数据失败: ' + err.message, duration: 5 })
      })
    }
  }
}
</script>

