<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item label="选择绩效月：">
        <el-select
          size="small"
          v-model="date"
          placeholder="请选择">
          <el-option
            v-for="item in list"
            :key="item.date"
            :label="item.name"
            :value="item.date"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          size="small"
          v-model="endTime"
          :picker-options="pickerTime"
          style="width: 400px"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择日期"/>
        <span class="remarks">{{ cutStart }}到{{ endTime }}的平均首次响应时长</span>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import util from '@/common/util'
const oneDay = 24 * 60 * 60 * 1000

export default {
  props: {
    initParams: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      list: [],
      date: '',
      time: '',
      endTime: '',
      startTime: '',
      cutStart: '',
      params: {},
      pickerTime: {
        disabledDate: time => {
          // 获取绩效起始日期
          return (
            time.getTime() > new Date(this.time) ||
            time.getTime() < new Date(this.startTime)
          )
        }
      }
    }
  },
  watch: {
    endTime (val) {
      let endTime = util.formatTime(new Date(val).getTime())
      this.$emit('getParams', {
        deadline: endTime.FullDate + ' 23:59:59'
      })
    },
    date (val) {
      let data = this.date.split('-')
      let maxFull = util.getDayTime(1)
      // let mm = maxFull.month + ''
      // mm = mm.length === 1 ? `0${mm}` : mm
      // let dd = maxFull.day + ''
      // dd = dd.length === 1 ? `0${dd}` : dd
      let maxDay = maxFull.FullDate
      let year = data[0]
      let month = parseInt(data[1])
      let endMonth = (month + '').length === 1 ? `0${month}` : month
      let start = ''
      let startYear = ''
      let startMonth = ''
      let end = ''
      end = `${year}-${endMonth}-19`
      end = new Date(end).getTime() > new Date(maxDay).getTime() ? maxDay : end
      startYear = month <= 1 ? year - 1 : year
      startMonth = month <= 1 ? 12 : month - 1
      startMonth =
        (startMonth + '').length === 1 ? `0${startMonth}` : startMonth
      start = `${startYear}-${startMonth}-19`
      this.startTime = start
      this.time = end
      // 返回初始化选中的值
      if (this.params.deadline) {
        this.endTime = this.initParams.deadline
        this.params.deadline = ''
      } else {
        this.endTime = end
      }
    },
    startTime (val) {
      let moreOne = util.formatTime(new Date(val).getTime() + oneDay)
      this.cutStart = moreOne.FullDate
    }
  },
  mounted () {
    this.params = { ...this.initParams }
    this.getList()
  },
  methods: {
    // 获取绩效月列表  可以选择2018年5月至今的绩效月 默认选中当前日期前一天的日期所在的绩效月
    getList () {
      let nowYear = util.getDayTime(1).year
      let getMonth = util.getDayTime(1).month
      let nowDay = util.getDayTime(1).day
      // 20号开始下个绩效月
      let nowMonth = getMonth
      if (nowDay >= 20) {
        nowMonth = getMonth + 1 <= 12 ? getMonth + 1 : 1
        nowYear = getMonth + 1 <= 12 ? nowYear : nowYear + 1
      }
      let max = 12
      let list = []
      for (let y = 2018; y <= nowYear; y++) {
        let m = 1
        if (y === 2018) {
          m = 5
        }
        if (y === nowYear) {
          max = nowMonth
        }
        for (m; m <= max; m++) {
          let mm = (m + '').length === 1 ? `0${m}` : m
          list.push({ date: `${y}-${mm}`, name: `${y}年${mm}月绩效月` })
        }
      }
      this.list = list
      if (this.params.deadline) {
        let initDate = this.initParams.deadline.split('-')
        this.date = `${initDate[0]}-${initDate[1]}`
      } else {
        this.date = list[list.length - 1].date
      }
    }
  }
}
</script>

