<template>
  <!-- im覆盖率 -->
  <div>
    <deadline-time @getParams="getParams" />
    <select-area
      :has-time="true"
      :end-time="params.deadline"
      @getInitList="search()"
      @getParams="getParams"/>
    <div class="exprot">
      <el-button
        :disabled="loading"
        size="small"
        type="primary"
        @click="searchButton()">查询</el-button>
      <el-button
        size="small"
        type="primary"
        @click="exprot">导出数据</el-button>
    </div>
    <div class="table-basic">
      <el-table
        v-loading="loading"
        :data="tableData"
        style="width: 100%">
        <el-table-column
          type="index"
          label="序号"
          min-width="60"
          align="center"/>
        <el-table-column
          prop="queryDate"
          label="时间"
          min-width="140"
          align="center">
          <template slot-scope="scope">
            截止{{ scope.row.queryDate }}
          </template>
        </el-table-column>
        <el-table-column
          prop="cityName"
          label="城市"
          min-width="80"
          align="center"/>
        <el-table-column
          prop="areaName"
          label="区域"
          min-width="120"
          align="center"/>
        <el-table-column
          prop="servicecenterName"
          label="服务中心"
          min-width="100"
          align="center"/>
        <el-table-column
          prop="userName"
          label="家庭顾问"
          min-width="100"
          align="center"/>
        <el-table-column
          prop="bindingStudentNumber"
          label="已绑定学员人数"
          min-width="100"
          align="center"/>
        <el-table-column
          prop="bindedCommunicateStudentNum"
          label="已绑定学员沟通人数"
          min-width="100"
          align="center"/>
        <el-table-column
          prop="bindedCommunicateStudentRate"
          label="已绑定学员沟通率"
          min-width="120"
          align="center" />
        <el-table-column
          prop="bindedMaintainStudentNum"
          label="已绑定维护中学员人数"
          min-width="120"
          align="center"/>
        <el-table-column
          prop="bindedMaintainCommunicateNum"
          label="已绑定维护中学员沟通人数"
          min-width="140"
          align="center"/>
        <el-table-column
          prop="bindedMaintainCommunicateRate"
          label="已绑定维护中学员沟通率"
          min-width="100"
          align="center" />
      </el-table>
    </div>
    <el-pagination
      size="small"
      :current-page="params.page"
      :total="total"
      :page-size="params.pagesize"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"/>
  </div>
</template>
<script>
import util from '@/common/util'
import selectArea from '@/components/dataReport/selectArea'
import deadlineTime from '@/components/dataReport/deadlineTime'
import verify from '../../../view/dataReport/verify.js'
import { list as getList } from '@/api/crmPanel'
import config from '../../../config'
import { runEnv } from '../../../libs/util'
const env = runEnv()
const urls = {
  // getList: '/xinxin/i/panel/list?classify=imcoverage',
  // exportUrl: '/xinxin/i/panel/download?classify=imcoverage',
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/download'
  // exportUrl: 'http://10.4.4.206:8667/crmPanelList/download'
}

export default {
  components: {
    selectArea,
    deadlineTime
  },
  data () {
    return {
      total: 0,
      params: {
        deadline: '',
        cities: '',
        areas: '',
        servcenters: '',
        collect: '',
        users: '',
        pagesize: 10,
        page: 1
      },
      historyParams: {},
      loading: false,
      tableData: []
    }
  },
  mounted () {},
  methods: {
    handleCurrentChange (val) {
      this.params.page = val
      this.historyParams.page = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pagesize = val
      this.historyParams.pagesize = val
      this.search(this.historyParams)
    },
    getParams (p) {
      let params = {
        ...this.params,
        ...p
      }
      this.params = params
    },
    searchButton () {
      this.params.page = 1
      this.search()
    },
    async search (historyParams) {
      let params = historyParams || { ...this.params }
      let val = verify(params)
      if (!val.check) {
        this.$message({
          message: val.msg,
          type: 'warning'
        })
        return
      }
      params.operate = 'imcoverage'
      this.historyParams = params
      try {
        this.loading = true
        const data = await getList(params)
        // let { data } = await this.$axios.post(urls.getList, params)
        this.tableData = data.records || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    exprot () {
      util.download(urls.exportUrl, this.historyParams)
    }
  }
}
</script>

<style scoped lang="less">
  .exprot {
    text-align: right;
  }
</style>
