<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item label="学员年级：">
        <select-all
          :data-list="gradeList"
          :init-select="grade"
          v-model="grade"
          all-key="全部年级"
          value-key="gradeId"
          label-key="gradeName"/>
        <span class="remarks">（可多选，默认选择全部）</span>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import selectAll from './selectAll'
import { getCallGradeList as getGradeList } from '../../api/crmPanel'
// const urls = {
//   getGradeList: '/xinxin/i/follow/call/grade'
// }
export default {
  components: {
    selectAll
  },
  data () {
    return {
      gradeList: [],
      grade: [],
      initGetList: true // 第一次请求
    }
  },
  watch: {
    grade (val) {
      this.$emit('getParams', { grades: val.join(',') })
      if (this.initGetList) {
        this.initGetList = false
        this.$emit('getInitList')
      }
    }
  },
  mounted () {
    this.getGrade()
  },
  methods: {
    async getGrade () {
      try {
        const data = await getGradeList()
        this.gradeList = data || []
        let ids = this.gradeList.map(item => item.gradeId)
        this.grade = ids
      } catch (err) {
        this.$Message.error({
          content: '获取学员可用年级失败：' + err.message
        })
      }
      // let { data } = await this.$axios.get(urls.getGradeList, {})
      // this.gradeList = data || []
      // let ids = this.gradeList.map(item => item.gradeId)
      // this.grade = ids
    }
  }
}
</script>

