<template>
  <!-- im聊天记录 -->
  <Layout class="content-data">
    <div class="content-data-card">
      <div>
        <el-form
          size="medium">
          <el-form-item label=" ">
            <el-col :span="8">
              <span>消息时间</span>
              <el-date-picker
                size="small"
                v-model="time"
                :picker-options="pickerTime"
                :clearable="false"
                type="daterange"
                style="width: 260px"
                range-separator="-"
                value-format="yyyy-MM-dd"
                start-placeholder="开始日期"
                end-placeholder="结束日期"/>
            </el-col>
            <el-col :span="5">
              <span>学员姓名</span>
              <el-input
                size="small"
                v-model="name"
                style="width: 140px"
                placeholder="请输入学员姓名"/>
            </el-col>
            <el-col :span="8">
              <span>信息关键词</span>
              <el-input
                size="small"
                v-model="keyVal"
                style="width: 160px"
                placeholder="请输入信息关键词"/>
              <el-button
                :disabled="loading"
                size="small"
                type="primary"
                class="search"
                @click="searchButton()">搜索</el-button>
            </el-col>
            <el-col
              :span="3"
              class="export">
              <el-button
                size="small"
                type="primary"
                @click="exportData">导出数据</el-button>
            </el-col>
          </el-form-item>

        </el-form>
      </div>
      <div
        ref="image"
        class="table-basic">
        <el-table
          v-loading="loading"
          :data="tableData"
          style="width: 100%">
          <el-table-column
            label="序号"
            type="index"
            min-width="80"
            align="center"/>
          <el-table-column
            prop="timestamp"
            label="时间"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="name"
            label="学员"
            min-width="80"
            align="center"/>
          <el-table-column
            prop="stuid"
            label="学员编号"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="action"
            label="接收/发送"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="msg"
            label="信息"
            min-width="200"
            align="center">

            <template slot-scope="scope">
              <img
                v-if="scope.row.type === 'image'"
                :src="scope.row.src"
                width="50"
                height="50">
              <span
                v-else-if="scope.row.type === 'text'">
                <span>{{ scope.row.msg }}</span>
              </span>
              <audio
                v-else-if="scope.row.type === 'audio'"
                class="audio"
                controls="controls">
                <source :src="scope.row.src">
              </audio>
              <a
                v-else-if="scope.row.type === 'url'"
                :href="scope.row.url"
                target="_blank">{{ scope.row.url }}</a>
            </template>

          </el-table-column>
          <el-table-column
            prop="state"
            label="状态"
            min-width="80"
            align="center"/>
        </el-table>
      </div>
      <el-pagination
        :current-page="params.page"
        :total="total"
        :page-size="params.pagesize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"/>
    </div>
  </Layout>
</template>
<script>
import 'viewerjs/dist/viewer.css'
import Viewer from 'viewerjs'
import util from '@/common/util'
import {
  imchatList
} from '@/api/crmPanel'
import config from '../../../config'
import { runEnv } from '../../../libs/util'
const env = runEnv()
const urls = {
  // getList: '/xinxin/i/panel/imchat/list',
  // exportUrl: '/xinxin/i/panel/imchat/download',
  // exportUrl: 'http://10.4.4.206:8667/crmPanelList/imChatDownload'
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/imChatDownload'
}
const oneDay = 24 * 60 * 60 * 1000
export default {
  components: {},
  data () {
    return {
      total: 0,
      tableData: [],
      loading: false,
      time: [],
      name: '',
      keyVal: '',
      params: {
        find_start_time: '',
        find_end_time: '',
        stu_userid: '',
        stu_name: '',
        msg_text: '',
        pagesize: 10,
        page: 1
      },
      oneSearch: false,
      historyParams: {},
      pickerMinDate: '',
      pickerTime: {
        onPick: ({ maxDate, minDate }) => {
          this.pickerMinDate = minDate.getTime()
          if (maxDate) {
            this.pickerMinDate = ''
          }
        },
        disabledDate: time => {
          const maxDay = new Date(new Date().getTime() - oneDay)
          if (this.pickerMinDate !== '') {
            const day30 = 29 * oneDay
            let maxTime = this.pickerMinDate + day30
            if (maxTime > maxDay) {
              maxTime = maxDay
            }
            return time.getTime() > maxTime
          }
          return time.getTime() > maxDay
        }
      },
      viewer: null
    }
  },
  watch: {
    time (val) {
      this.params.find_start_time = val[0]
      this.params.find_end_time = val[1]
      if (!this.oneSearch) {
        this.search()
        this.oneSearch = true
      }
    },
    name (val) {
      this.params.stu_name = val
    },
    keyVal (val) {
      this.params.msg_text = val
    }
  },
  mounted () {
    this.$store.commit('data/setShowImTag', 'reply')
    this.typeWeek()
    if (this.$route.query.id) {
      this.params.stu_userid = this.$route.query.id
    }
    this.$nextTick(() => {
      this.viewer = new Viewer(this.$refs['image'], {
        viewed () {
          this.viewer.zoomTo(0.3)
        },
        navbar: 0,
        toolbar: {
          zoomIn: 4,
          zoomOut: 4,
          oneToOne: 0,
          reset: 4,
          prev: 0,
          play: {
            show: 0,
            size: 'large'
          },
          next: 0,
          rotateLeft: 4,
          rotateRight: 4,
          flipHorizontal: 4,
          flipVertical: 4
        }
      })
    })
  },
  methods: {
    searchButton () {
      this.params.page = 1
      this.search()
    },
    async search (historyParams) {
      let params = historyParams || { ...this.params }
      this.historyParams = params
      // let { data } = await this.$axios.get(urls.getList, {
      //   params: params
      // })
      this.loading = true
      try {
        let data = await imchatList(params)
        const rows = data.records
        const msgs = []
        rows.forEach(row => {
          let msg = row.msg || ''
          row.time = row.act_time_stu || row.act_time_tch
          const audioExts = [
            'mp3',
            'wav',
            'aac',
            'wma',
            'wmv',
            'amr',
            'mp2',
            'flac',
            'vorbis',
            'ac3'
          ]
          const imageExts = ['jpg', 'jpeg', 'png', 'bmp', 'gif']
          try {
            msg = JSON.parse(msg)
          } catch (e) {}
          if (typeof msg === 'object') {
            const ext = msg.ext && msg.ext.toLowerCase()
            if (audioExts.includes(ext)) {
              // const mp3Url = this.$store.state.nim.audioToMp3({url: msg.url});
              row.type = 'audio'
              row.src = msg.url
              row.dur = msg.dur || 0
              row.md5 = msg.md5
            } else if (imageExts.includes(ext)) {
              row.type = 'image'
              row.src = msg.url
              row.w = msg.w
              row.h = msg.h
            }
          } else {
            const urlReg = /(https?|ftp|file):\/\/[-A-Za-z0-9+&@#/%?=~_|!:,.;]+[-A-Za-z0-9+&@#/%=~_|]/
            if (urlReg.test(msg)) {
              row.type = 'url'
              row.url = msg
            } else {
              row.type = 'text'
              row.msg = row.msg
            }
          }
          msgs.push(row)
        })

        this.tableData = msgs || []
        this.total = data.totalCount

        this.$nextTick(() => {
          this.viewer.update()
        })
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    // 维度周 默认选择当周到前一天 7天内
    typeWeek () {
      let end = util.getDayTime(1)
      let start = util.getDayTime(7)
      this.time = [start.FullDate, end.FullDate]
    },
    handleCurrentChange (val) {
      this.params.page = val
      this.historyParams.page = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pagesize = val
      this.historyParams.pagesize = val
      this.search(this.historyParams)
    },
    exportData () {
      const params = Object.assign({}, this.historyParams, {
        empNo: this.$store.state.user.userInfo.id
      })
      delete params.page
      delete params.pagesize
      util.download(urls.exportUrl, params, 'get')
    }
  }
}
</script>

<style scoped lang="less">
  @import '../common';
  /*.content-data {*/
    /*padding: 20px;*/
  /*}*/
  .search {
    margin-left: 20px;
  }
  .audio {
    display: inline-block;
    width: 100%;
    height: 30px;
  }
  .audio:focus {
    outline-width: 0px;
  }
</style>
