  methods: {
    // 显示绩效周时间范围
    // performanceOfWeek () {
    //   const lastFriday = moment().week(moment().week() - 1).weekday(5).format('YYYY-MM-DD')
    //   const today = moment().format('YYYY-MM-DD')
    //   const dayOfWeek = moment().day()
    //   if (dayOfWeek === 6 || dayOfWeek === 0) {
    //     this.performanceOfWeekContent = moment().weekday(5).format('YYYY-MM-DD') + ' ~ ' + today
    //   } else {
    //     this.performanceOfWeekContent = lastFriday + ' ~ ' + today
    //   }
    // },
    // 请求数据趋势图数据
    queryHandler (dateType, projectType) {
      let _this = this
      let { baseDataId, crmDeptId, empNo, crmRoleId, cityCode } = this.crmUserInfo
      let param = {
        dateType,
        projectType: this.itemTransQueryPath[projectType],
        crmRoleId,
        empNo,
        baseDataId,
        crmDeptId,
        cityCode
      }
      getResourceTendencyList(param).then(res => {
        let data = res[projectType]
        let xAxisData = data.map(item => item.date)
        if (projectType !== 'dailyPayList' && projectType !== 'dailyResourceList') {
          let newPerson = data.map(item => item.newPerson)
          let oldPerson = data.map(item => item.oldPerson)
          Vue.set(_this[projectType], 'newPerson', newPerson)
          Vue.set(_this[projectType], 'oldPerson', oldPerson)
        }
        if (projectType !== 'dailyResourceList') {
          let total = data.map(item => projectType === 'dailyPayList' ? item.payTotalSum : item.totalCount)
          Vue.set(_this[projectType], 'total', total)
        }
        let series = this.ehartsItem[projectType].map(item => {
          let result = data.map(row => row[item.key])
          return {
            name: item.value,
            type: 'line',
            showSymbol: false,
            // emphasis: {
            //   focus: 'series'
            // },
            smooth: true,
            data: result
          }
        })
        Vue.set(_this[projectType], 'xAxisData', xAxisData)
        Vue.set(_this[projectType], 'series', series)
      }).catch(err => {
        this.$Message.error({ content: '查询数据趋势数据: ' + err.message, duration: 5 })
      })
    },
    initMonthSelect () {
      getTotalDate().then(data => {
        this.monthList = data
        this.selectMonth = data[data.length - 1]
        this.queryParams.cityCode = this.crmUserInfo.cityCode
        this.queryParams.queryDate = data[data.length - 1]
        this.queryParams.baseDataId = this.crmUserInfo.baseDataId
        this.queryParams.crmRoleId = this.crmUserInfo.crmRoleId
        this.queryParams.empNo = this.userInfo.empNo
        this.queryParams.crmDeptId = this.crmUserInfo.crmDeptId
        this.$nextTick(() => {
          this.query()
        })
      }).catch(err => {
        this.$Message.error({ content: '查询月份选项失败: ' + err.message, duration: 5 })
      })
    },
    queryResourceDetails () {
      resourceDetails(this.queryParams).then(data => {
        this.resourceDetails = data
      }).catch(err => {
        this.$Message.error({ content: '查询资源看板详情统计失败: ' + err.message, duration: 5 })
      })
    },
    // 请求漏斗数据
    queryFunnelDate () {
      funnel(this.queryParams).then(data => {
        this.funnelData = data
      }).catch(err => {
        this.$Message.error({ content: '查询漏斗数据失败: ' + err.message, duration: 5 })
      })
    },
    selectChange (val) {
      if (val) {
        if (val === '13' || val === '15' || val === '16') {
          this.selectMonth = ''
        }
        this.queryParams.queryDate = val
        this.query()
      }
    },
    query () {
      this.$refs.flowPoll.initView()
      this.queryResourceDetails()
      this.queryFunnelDate()
    },
    getFlowData () {
      this.updateTime = this.$refs.flowPoll.flowData.updateTime
    }
  }
}
</script>

