<template>
  <div class="chart-item" :class="noData ? 'no-data':''">
    <div class="chart-name">{{ name }}</div>
    <div class="chart" ref="chart"></div>
    <div class="chart-num">{{ numText }}</div>
  </div>
</template>

<script>
// 引入 echarts 核心模块，核心模块提供了 echarts 使用必须要的接口。
import * as echarts from 'echarts/core'
// 引入柱状图图表，图表后缀都为 Chart
import {
  BarChart
} from 'echarts/charts'
// 引入 Canvas 渲染器，注意引入 CanvasRenderer 或者 SVGRenderer 是必须的一步
import {
  CanvasRenderer
} from 'echarts/renderers'
import { toThousands } from '@/utils/utils'
// 注册必须的组件
echarts.use(
  [BarChart, CanvasRenderer]
)
export default {
  name: 'ChartItem',
  props: {
    name: String,
    num: [ Number, Array ],
    max: Number,
    noData: Boolean
  },
  computed: {
    numText () {
      if (typeof this.num === 'number') {
        return toThousands(this.num)
      }
      if (typeof this.num === 'object') {
        return this.num.map(n => toThousands(n)).join('/')
      }
      return ''
    }
  },
  watch: {
    max (val) {
      if (val >= 0) {
        this.renderChart()
      }
    }
  },
  methods: {
    renderChart () {
      this.$nextTick(() => {
        let myChart = echarts.getInstanceByDom(this.$refs.chart)
        if (myChart == null) { // 如果不存在，就进行初始化
          myChart = echarts.init(this.$refs.chart)
          window.addEventListener('resize', () => {
            myChart.resize()
          })
        }
        const option = {
          color: ['#5B8FF9', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc'],
          legend: {
            show: false,
            data: ['Direct']
          },
          grid: {
            left: 1,
            right: 1,
            top: 1,
            bottom: 1
          },
          xAxis: {
            show: false,
            max: this.max
          },
          yAxis: {
            show: false,
            type: 'category',
            data: ['Mon']
          }
        }
        let series = []
        if (typeof this.num === 'object') {
          series = this.num.map(n => {
            return {
              type: 'bar',
              stack: 'total',
              showBackground: true,
              data: [n]
            }
          })
        } else if (typeof this.num === 'number') {
          series = [{
            type: 'bar',
            stack: 'total',
            showBackground: true,
            data: [this.num]
          }]
        }
        option.series = series
        // console.log(this.name)
        // console.log(option)
        // 绘制图表
        myChart.setOption(option)
      })
    }
  },
  mounted () {
    this.renderChart()
  }
}
</script>

<style lang="less" scoped>
  .chart-item {
    display: flex;
    align-items: center;
    color: rgba(0, 0, 0, 0.8);
    &.no-data {
      color: #EDEDED
    }
    .chart-name {
      width: 70px;
      margin-right: 16px;
      font-family: PingFang SC;
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
    }
    .chart {
      flex: 1;
      height: 16px;
      margin-right: 16px;
    }
    .chart-num {
      width: 70px;
      font-family: PingFang SC;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 22px;
      text-align: right;
    }
  }
</style>
