<template>
    <div style="width:100%;padding: 10px;background:#f5f7f9">
      <Form inline :label-width="80" style="margin-bottom:10px;padding-top:16px;width:100%;background: #ffffff">
        <FormItem label="年份">
          <Select v-model="formData.year" clearable style="width:100px">
            <Option v-for="year in yearList" :key="year" :value="year">{{year}}</Option>
          </Select>
        </FormItem>
        <FormItem label="学期">
          <Select v-model="formData.termId" clearable style="width:100px">
            <Option v-for="item in termList" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="授课类型">
          <Select v-model="formData.classType" clearable style="width:100px">
            <Option v-for="item in classTypeList" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="操作人">
          <Select v-model="formData.empNo" clearable style="width:100px" filterable>
            <Option v-for="item in adviserList" :key="item.empNo" :value="item.empNo">{{ item.userName }}</Option>
          </Select>
        </FormItem>
        <FormItem label="操作日期">
          <DatePicker :editable="false" @on-change="dateChange" type="daterange" separator="~" style="width:200px"/>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="search">查询</Button>
        </FormItem>
      </Form>
      <Table stripe :columns="tableColumns" :data="tableData" :loading="tableLoading">
        <template slot-scope="{ row }" slot="operation">
          <a style="margin-right: 5px;" @click="recovery(row)">恢复</a>
        </template>
        <template slot-scope="{ row }" slot="registId">
          <Button size="small" type="primary" ghost @click="copy(row.registId)" style="margin-right: 5px">复制</Button>
          <span>{{row.registId}}</span>
        </template>
        <template slot-scope="{ row }" slot="classtimeNames">
          <Poptip v-if="row.classtimeNames && row.classtimeNames.length > 15" trigger="hover" width="300" word-wrap  :content="row.classtimeNames" transfer>
            {{getNameLength(row.classtimeNames)}}
          </Poptip>
          <template v-else>
            {{row.classtimeNames || '--'}}
          </template>
        </template>
        <template slot-scope="{ row }" slot="remark">
          <Poptip v-if="row.remark && row.remark.length > 15" trigger="hover" width="300" word-wrap  :content="row.remark" transfer>
            {{getNameLength(row.remark)}}
          </Poptip>
          <template v-else>
            {{row.remark || '--'}}
          </template>
        </template>
      </Table>
      <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
        <div style="float: right;">
          <Page
            :total="totalCount"
            :page-size="formData.pageCount"
            :current="formData.page"
            show-elevator
            show-sizer
            show-total
            @on-page-size-change="pageSizeChange"
            @on-change="changePage"></Page>
        </div>
      </div>
      <tal-teacher-details
        ref="teacherRef"
        :schoolCode="teacherDetail.cityCode"
        :teacherId="teacherDetail.id"
        :type="teacherDetail.type"
        :creatorEmpNo="teacherDetail.empNo"
      ></tal-teacher-details>
      <tal-class-detail
        :class-id="classDetail.id"
        :show="classDetail.show"
        @close="classDetail.show = false">
      </tal-class-detail>
    </div>
</template>

<script>
import TeacherMixin from '@/mixins/teacherDetail'
import ClassMixin from '@/mixins/classDetail'
import { copyText } from '@/utils/utils'
import {
  getYearList,
  getLinkageNational,
  getClassType
} from '@/api/bd'
import {
  getCancellationsClassPage,
  recoverCancelRegisters
} from '@/api/register'
import { getUsersLevelInfo } from '@/api/crm'
export default {
  name: 'cancelOrderKanBan',
  mixins: [TeacherMixin, ClassMixin],
  data () {
    return {
      formData: {
        year: '',
        termId: '',
        classType: '',
        empNo: '',
        cancelStartDate: '',
        cancelEndDate: '',
        page: 1,
        pageCount: 10
      },
      totalCount: 0,
      tableLoading: false,
      tableData: [],
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授',
        8: '线上班'
      },
      tableColumns: Object.freeze([
        {
          title: '操作',
          align: 'center',
          minWidth: 100,
          slot: 'operation'
        },
        {
          title: '报名ID',
          align: 'center',
          minWidth: 150,
          slot: 'registId'
        },
        {
          title: '班级名称',
          key: 'name',
          align: 'center',
          minWidth: 200,
          render: (h, params) => {
            return h('div', [
              h('a', {
                on: {
                  click: () => {
                    this.showClassDetail(params.row.classId)
                  }
                }
              }, params.row.className),
              h('div', {
                style: {
                  'text-align': 'right',
                  'display': 'inline-block'
                }
              }, [
                h('Tag', {
                  style: {
                    'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                  },
                  props: {
                    color: 'green'
                  }
                }, '全国班')
              ])
            ])
          }
        },
        {
          title: '授课类型',
          key: 'classType',
          align: 'center',
          // minWidth: 100,
          render: (h, params) => {
            return h('span', {
              style: {}
            }, this.classTypeMap[params.row.classType])
          }
        },
        {
          title: '报班状态',
          align: 'center',
          // minWidth: 100,
          render: (h, params) => {
            return h('span', {
              style: {}
            }, '已撤单')
          }
        },
        {
          title: '学科',
          key: 'subjectNames',
          align: 'center'
          // minWidth: 100
        },
        {
          title: '主讲教师',
          key: 'teacherNames',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            const tNames = params.row.teacherNames.split(',')
            const tIds = params.row.teacherIds.split(',')
            return h('div', tNames.map((n, index) => {
              return h('a', {
                on: {
                  click: () => {
                    this.showTeacherDetail(this.cityCode, tIds[index], '0')
                  }
                }
              }, n)
            }))
          }
        },
        {
          title: '辅导教师',
          key: 'tutorRealName',
          align: 'center'
          // minWidth: 100
        },
        {
          title: '开课日期',
          key: 'startDate',
          align: 'center'
          // minWidth: 140
        },
        {
          title: '结课日期',
          key: 'endDate',
          align: 'center'
          // minWidth: 140
        },
        {
          title: '上课时间',
          slot: 'classtimeNames',
          align: 'center'
          // minWidth: 150
        },
        {
          title: '教室',
          key: 'classroomName',
          align: 'center'
          // minWidth: 100
        },
        {
          title: '上课进度',
          align: 'center',
          // minWidth: 100,
          render: (h, params) => {
            return h('span', {
            }, params.row.passedCount + '/' + params.row.classCount)
          }
        },
        {
          title: '总金额',
          key: 'price',
          align: 'center'
          // minWidth: 120
        },
        {
          title: '变动记录',
          align: 'center',
          // minWidth: 200,
          render: (h, params) => {
            return h('Poptip', {
              props: {
                trigger: 'hover',
                width: '300',
                'word-wrap': true,
                transfer: true,
                content: params.row.classChangeName
              }
            }, params.row.businessType !== 1 ? this.getNameLength(params.row.classChangeName) : '——')
          }
        },
        {
          title: '缴费截止日期',
          key: 'payEndDate',
          align: 'center'
          // minWidth: 120
        },
        {
          title: '操作人',
          key: 'creatorName',
          align: 'center'
          // minWidth: 100
        },
        {
          title: '操作时间',
          key: 'createTime',
          align: 'center'
          // minWidth: 150
        },
        {
          title: '备注',
          slot: 'remark',
          align: 'center'
          // minWidth: 150
        }
      ]),
      yearList: [],
      termList: [],
      classTypeList: [],
      adviserList: []
    }
  },
  computed: {
    cityCode () {
      return this.$store.state.roleRelativeData.userInfo.cityCode
      // return '0371'
    }
  },
  methods: {
    getYearList () {
      getYearList().then((data) => {
        this.yearList = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取年份失败:' + err.message, duration: 5 })
      })
    },
    getTermList () {
      let req = {
        cityCode: this.cityCode,
        queryType: 't'
      }
      getLinkageNational(req).then((data) => {
        this.termList = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取学期失败:' + err.message, duration: 5 })
      })
    },
    getTypeListHandler () {
      let req = {}
      getClassType(req).then(res => {
        this.classTypeList = res
      }).catch(err => {
        this.$Message.warning({ content: '获取授课类型失败:' + err.message, duration: 5 })
      })
    },
    getAdviserList () {
      getUsersLevelInfo({
        areaCode: this.cityCode
      }).then(data => {
        this.adviserList = data
      }).catch(err => {
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
    },
    dateChange (val) {
      this.formData.cancelStartDate = val[0]
      this.formData.cancelEndDate = val[1]
    },
    search () {
      this.formData.page = 1
      this.tableData = []
      this.getTableData()
    },
    getTableData () {
      this.tableLoading = true
      this.formData.areaCode = this.cityCode
      getCancellationsClassPage(this.formData).then(res => {
        this.tableData = []
        if (res) {
          this.totalCount = res.totalCount
          if (res.records) {
            this.tableData = res.records
            this.tableData.forEach((e) => {
              if (e.startDate) {
                e.startDate = e.startDate.substring(0, 10)
              }
              if (e.endDate) {
                e.endDate = e.endDate.substring(0, 10)
              }
            })
          }
        }
      }).catch((err) => {
        this.tableData = []
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    recovery (item) {
      let req = {
        cityCode: this.cityCode,
        registId: item.id
      }
      this.$Modal.confirm({
        title: '恢复',
        content: '你确定恢复吗？',
        onOk: () => {
          recoverCancelRegisters(req).then((res) => {
            this.$Message.success('恢复成功')
            this.getTableData()
          }).catch((err) => {
            this.$Message.warning({ content: '恢复失败:' + err.message, duration: 5 })
          })
        }
      })
    },
    // 分页
    changePage (num) {
      this.formData.page = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.formData.pageCount = size
      this.getTableData()
    },
    getNameLength (name, len = 15) {
      if (name && name.length > len) {
        return name.substring(0, len) + '...'
      } else {
        return name
      }
    },
    copy (text) {
      copyText(text)
    }
  },
  mounted () {
    this.getYearList()
    this.getTermList()
    this.getTypeListHandler()
    this.getAdviserList()
  }
}
</script>

