<template>
  <!-- 学员资源统计 -->
  <Layout class="content-data">
    <div class="content-data-card">
      <deadline-time @getParams="getParams"/>
      <select-area
        :has-time="true"
        :end-time="params.deadline"
        @getInitList="getInitList()"
        @getParams="getParams"/>
      <grade
        @getParams="getParams"
        @getInitList="getInitList()"/>
      <div class="exprot">
        <el-button
          :disabled="loading"
          size="small"
          type="primary"
          @click="searchButton()">查询</el-button>
        <el-button
          size="small"
          type="primary"
          @click="exprot">导出数据</el-button>
      </div>
      <div class="table-basic">
        <el-table
          v-loading="loading"
          width="100%"
          :data="tableData">
          <el-table-column
            label="序号"
            type="index"
            min-width="60"
            align="center"/>
          <el-table-column
            prop="queryDate"
            label="时间"
            min-width="140"
            align="center">
            <template slot-scope="scope">
              截止{{ scope.row.queryDate }}
            </template>
          </el-table-column>
          <el-table-column
            prop="cityName"
            label="城市"
            min-width="80"
            align="center"/>
          <el-table-column
            prop="areaName"
            label="区域"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="servicecenterName"
            label="服务中心"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="userName"
            label="家庭顾问"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="studentSumNumber"
            label="学员总人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="newStudentNumber"
            label="新学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="sleepStudentNumber"
            label="沉睡老学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="readingStudentNumber"
            label="在读学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="readingNewNumber"
            label="在读新学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="readingOldNumber"
            label="在读老学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="notBindingStudentNumber"
            label="未绑定学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="bindedStudentNumber"
            label="已绑定学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="bindedMaintainStudentNum"
            label="已绑定维护中学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="bindedMaintainActiveNum"
            label="已绑定维护中-活跃学员人数"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="bindedMaintainWarningNum"
            label="已绑定维护中-预警学员人数"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="bindedSleepStudentNum"
            label="已绑定沉睡学员人数"
            min-width="100"
            align="center"/>
        </el-table>
      </div>
      <el-pagination
        :current-page="params.page"
        :total="total"
        :page-size="params.pagesize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"/>
    </div>
  </Layout>
</template>
<script>
import verify from './verify.js'
import util from '@/common/util'
import selectArea from '@/components/dataReport/selectArea'
import grade from '@/components/dataReport/grade'
import deadlineTime from '@/components/dataReport/deadlineTime'
import { list as getList } from '@/api/crmPanel'

import config from '../../config'
import { runEnv } from '../../libs/util'
const env = runEnv()

const urls = {
  // getList: '/xinxin/i/panel/list?classify=student-resource',
  // exportUrl: '/xinxin/i/panel/download?classify=student-resource',
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/download'
  // exportUrl: 'https://gateway-one.speiyou.cn/SPLAT/crmPanelList/download'
}
export default {
  name: 'dataReportResource',
  components: {
    selectArea,
    grade,
    deadlineTime
  },
  data () {
    return {
      total: 0,
      params: {
        deadline: '',
        cities: '',
        areas: '',
        servcenters: '',
        grades: '',
        collect: '',
        users: '',
        pagesize: 10,
        page: 1
      },
      historyParams: {},
      tableData: [],
      loading: false,
      value1: ''
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.params.page = val
      this.historyParams.page = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pagesize = val
      this.historyParams.pagesize = val
      this.search(this.historyParams)
    },
    getParams (p) {
      let params = {
        ...this.params,
        ...p
      }
      this.params = params
    },
    searchButton () {
      this.params.page = 1
      this.search()
    },
    async search (historyParams) {
      let params = historyParams || { ...this.params }
      let val = verify(params)
      if (!val.check) {
        this.$message({
          message: val.msg,
          type: 'warning'
        })
        return
      }

      let tipMsg = ''
      if (!params.deadline) {
        tipMsg = '截止日期不能为空'
      }
      if (!params.grades) {
        tipMsg = '学员年级不能为空'
      }
      if (tipMsg) {
        this.$message({
          message: tipMsg,
          type: 'warning'
        })
        return
      }
      params.operate = 'student-resource'
      this.historyParams = params
      // let { data } = await this.$axios.post(urls.getList, params)
      // this.tableData = data.records || []
      // this.total = data.totalCount

      try {
        this.loading = true
        const data = await getList(params)
        this.tableData = data.records || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    exprot () {
      util.download(urls.exportUrl, this.historyParams)
    },
    getInitList () {
      if (this.params.collect === '4' || this.params.collect === '5') {
        if (this.params.users && this.params.grades) {
          this.search()
        }
      } else {
        if (this.params.servcenters && this.params.grades) {
          this.search()
        }
      }
    }
  }
}
</script>

