<template>
  <div class="context_box">
    <div class='search_box'>
    <div class="select_content" v-close="{data:'showCity'}">
        <div class="select_top" >
          <span :class="showCity?'select_box bulecolor ':'select_box nomalcolor'" >{{currentcity}}</span>
          <span :class="showCity?'select_icon borderdownbulecolor':'select_icon borderdowncolor'"  @click="openValue('city')"></span>
        </div>
        <div class="select_list" v-show="showCity">
          <ul >
            <li @click="getVal(item,'city')" v-for="(item) in cityList" :key="item.key">{{ item.label }}</li>
          </ul>
        </div>
    </div>
    <div class="select_content right" v-close="{data:'showSchool'}">
        <div class="select_top" >
          <span :class="showSchool?'select_box bulecolor ':'select_box nomalcolor'"  >{{currentschool}}</span>
          <span :class="showSchool?'select_icon borderdownbulecolor':'select_icon borderdowncolor'" @click="openValue('school')"></span>
        </div>
        <div class="select_list" style=" width: 80px;" v-show="showSchool">
          <ul >
            <li @click="getVal(item,'school')" v-for="(item) in schoolList" :key="item.key">{{ item.label }}</li>
          </ul>
        </div>
    </div>
    </div>
    <div>
      <div class="header">
        <div>排名</div>
        <div class="sencond">姓名</div>
        <div class="cityInfo">分校</div>
        <div>完成量</div>
      </div>
      <div class="c_box" v-if="rankData&&rankData.resourceRankingList">
        <div
        :class="item.id>20?'bg body':'body'"
        v-for="(item,index) in rankData.resourceRankingList"
        :key="item.id+item.empName"
      >
        <div><span :class="[index<=2?indexTcolor[index]:'','nomalNum']">{{item.id}}</span></div>
        <div class="sencond"><span>{{item.empName}}</span><span v-if="item.empNo==userInfo.empNo" class="myself">我</span>
        </div><div class="cityInfo">{{item.cityName}}</div><div>{{item.finishTotal+indexTranUnit[city]}}</div>
      </div>
      </div>
      <div class='footer' v-if="userInfo.crmRoleId==5">
        <span>{{`Hi～${userInfo.empName}老师` }}</span>
        <p v-if="rankData&&rankData.encourageType==1">{{`${currentschool}有${rankData.branchSchoolTotal}名顾问老师，您目前的${currentcity}完成量在第${rankData.branchSchoolRanking}名，真棒哦～`}}</p>
        <p v-if="rankData&&rankData.encourageType==2">{{`${currentschool}有${rankData.branchSchoolTotal}名顾问老师，您目前的${currentcity}完成量在第${rankData.branchSchoolRanking}名，有希望赶超上一个老师哦～`}}</p>
        <p v-if="rankData&&rankData.encourageType==3">{{`${currentschool}有${rankData.branchSchoolTotal}名顾问老师，您目前的${currentcity}完成量在第${rankData.branchSchoolRanking}名，努努力，不做垫底～`}}</p>
      </div>
    </div>

  </div>
</template>

<script>
import { getResourceRankingList } from '@/api/kanban.js'
import { mapState } from 'vuex'
export default {
  directives: {
    close: {
      inserted (el, binding, vnode) {
        window.addEventListener('click', function (e) {
          if (!el.contains(e.target)) {
            vnode.context[binding.value.data] = false
          }
        })
      }
    }
  },
  data () {
    return {
      cityList: [{
        value: '1',
        label: '长期班缴费人次'
      }, {
        value: '2',
        label: '转化产品参与人数'
      }, {
        value: '3',
        label: '加微人头'
      }
      ],
      // schoolList: [{
      //   value: '1',
      //   label: '同分校'
      // }, {
      //   value: '2',
      //   label: '同T次'
      // }, {
      //   value: '3',
      //   label: '全国'
      // }],
      schoolList: [{
        value: '1',
        label: '同分校'
      }, {
        value: '3',
        label: '全国'
      }],
      city: 1,
      school: 1,
      indexTcolor: {
        '0': 'firstNum',
        '1': 'secondNum',
        '2': 'thirdNum'
      },
      indexTranUnit: {
        1: '人次',
        2: '人数',
        3: '人头'
      },
      rankData: {},
      queryDate: 13,
      showCity: false,
      showSchool: false,
      itemToField: {
        'city': 'showCity',
        'school': 'showSchool'
      },
      currentschool: '同分校',
      currentcity: '长期班缴费人次'
    }
  },
  props: ['date'],
  created () {

  },
  computed: {
    ...mapState('roleRelativeData', ['userInfo'])
  },
  methods: {
    openValue (type) {
      this[this.itemToField[type]] = !this[this.itemToField[type]]
    },
    getVal (item, type) {
      let value = item.value
      let text = item.label
      this[type] = value
      this['current' + type] = text
      this.openValue(type)
      this.search()
    },
    async search () {
      let params = {
        rankingType: this.city,
        dimensionalityType: this.school,
        empNo: this.userInfo.empNo,
        cityCode: this.userInfo.cityCode,
        queryDate: this.queryDate,
        crmRoleId: this.userInfo.crmRoleId
      }
      try {
        this.rankData = await getResourceRankingList(params)
      } catch (err) {
        this.$Message.error(err.message)
      }
    }
  },
  watch: {
    date: {
      handler: function (val) {
        this.queryDate = val
        this.search()
      }
    }
  }
}
</script>
