<template>
  <div class="content_box">
    <div class="btn_list">
      <el-radio-group
        v-model="btnType"
        size="mini"
        @change="changeTime"
      >
        <el-radio-button label="1" >近一个月</el-radio-button>
        <el-radio-button label="2" >近两个月</el-radio-button>
        <el-radio-button label="3" >近三个月</el-radio-button>
        <el-radio-button label="4" >近半年</el-radio-button>
      </el-radio-group>
    </div>
    <div
      style="height:395px;"
      :id="'echarts'+type"
      class="echarts"
    ></div>
  </div>
</template>

<script>
// 引入 echarts 核心模块，核心模块提供了 echarts 使用必须要的接口。
import * as echarts from 'echarts/core'
// 引入折线图图表，图表后缀都为 Chart
import {
  LineChart
} from 'echarts/charts'
// 引入提示框，标题，直角坐标系组件，组件后缀都为 Component
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  LegendComponent
} from 'echarts/components'
// 引入 Canvas 渲染器，注意引入 CanvasRenderer 或者 SVGRenderer 是必须的一步
import {
  CanvasRenderer
} from 'echarts/renderers'
// 注册必须的组件
echarts.use(
  [TitleComponent, TooltipComponent, GridComponent, LegendComponent, LineChart, CanvasRenderer]
)
export default {
  props: ['trendEchartsData', 'type'],
  data () {
    return {
      btnType: '1',
      option: {
        color: ['#D75E44'],
        title: {
          text: '测试',
          textStyle: {
            fontSize: 16,
            fontWeight: 500,
            lineHeight: 24,
            color: 'rgba(0,0,0,0.9)'
          },
          subtext: 'danwei',
          subtextStyle: {
            verticalAlign: 'bottom',
            lineHeight: 40
          }
        },
        tooltip: {
          trigger: 'axis',
          backgroundColor: 'rgba(0, 0, 0, 0.85)',
          axisPointer: {
            type: 'line',
            label: {
              backgroundColor: '#9E9E9E',
              snap: true
            },
            lineStyle: {
              cap: 'round'
            }
          },
          formatter: (params) => {
            let { axisValue } = params[0]
            let info = ''
            let newPerson = ''
            let total = ''
            let oldPerson = ''
            params.forEach(item => {
              let { data, seriesName, color, dataIndex } = item
              total = !this.trendEchartsData.noTotal ? '<div style="font-size:14px;display:flex; justify-content: space-between;font-weight:600;color:#fff;margin-bottom:16px"><span  style="font-size: 12px;color:rgba(255, 255, 255, 0.85)";line-height:16px >' + axisValue + '</span><div style="line-height:22px;">总计<span style="display:inline-block;margin-left:8px;">' + this.trendEchartsData['total'][dataIndex] + ' </span> </div></div>' : '<div style="font-size:14px;font-weight:600;color:#fff;margin-bottom:16px"><span  style="font-size: 12px;color:rgba(255, 255, 255, 0.85)";line-height:16px >' + axisValue + '</span></div>'
              let marker = `<span style="display:inline-block;margin:0 6px 1px 0;border-radius:6px;width:6px;height:6px;background-color:${color}"></span>`
              info += '<div style="display: flex;justify-content: space-between;font-size: 14px;color: #fff;" > <span style="font-size: 12px;rgba(255, 255, 255, 0.85)">' + marker + seriesName + '</span> <span style="font-weight:500">' + data + '</span> </div>'
              newPerson = !this.trendEchartsData.noNewPerson ? '<div style="margin:12px 0 8px 0 ;font-size: 14px;color: #fff;" > <span style="color:rgba(255, 255, 255, 0.85);font-size: 12px;margin-right:12px">新生</span> <span style="font-weight:500">' + this.trendEchartsData['newPerson'][dataIndex] + '</span> </div>' : ''
              oldPerson = !this.trendEchartsData.noNewPerson ? '<div style="font-size: 14px;color: #fff;" > <span style="font-size: 12px;margin-right:12px;rgba(255, 255, 255, 0.85)">老生</span> <span style="font-weight:500">' + this.trendEchartsData['oldPerson'][dataIndex] + '</span> </div>' : ''
            })
            let str = '<div style="padding:8px; width:212px;box-sizing:border-box">' + total + info + (!this.trendEchartsData.noNewPerson ? (newPerson + oldPerson) : '') + '</div>'
            return str
          }
        },
        legend: {
          data: ['加微量'],
          borderRadius: 2,
          top: '10%',
          right: 0,
          icon: 'circle',
          itemWidth: 6,
          itemGap: 40,
          textStyle: {
            fontSize: 12,
            color: '#606366'
          }
        },
        grid: {
          top: '20%',
          left: '1%',
          right: '0%',
          bottom: '5%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
          axisLine: {
            onZero: true,
            lineStyle: {
              color: 'rgba(0, 0, 0, 0.25)'
            }
          },
          axisLabel: {
            textStyle: {
              color: '#606366'
            }
          }
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: '加微量',
            type: 'line',
            stack: false,
            showSymbol: false,
            emphasis: {
              focus: 'series'
            },
            noNewPerson: true,
            data: [120, 132, 210]
          }
        ]
      }
    }
  },
  computed: {
  },
  mounted () {

  },
  methods: {
    changeTime (e) {
      this.$emit('queryData', e, this.type)
    }
  },
  watch: {
    trendEchartsData: {
      handler: function (val) {
        this.$nextTick(() => {
          let { lineColor, titleText, legendData, xAxisData, xAxisName, series } = val
          let myChart = echarts.getInstanceByDom(document.getElementById('echarts' + this.type))
          if (!myChart) {
            myChart = echarts.init(document.getElementById('echarts' + this.type))
          }
          this.option.title.text = titleText
          this.option.title.subtext = xAxisName
          this.option.color = lineColor
          this.option.xAxis.data = xAxisData
          this.option.legend.data = legendData
          this.option.series = series
          myChart.setOption(this.option)
          window.addEventListener('resize', function () { myChart.resize() })
        })
      },
      deep: true
    }
  }
}
</script>

<style scoped lang="less">
.content_box {
  position: relative;
  padding: 24px;
  color: rgba(0, 0, 0, 0.9);
  overflow: hidden;
  width: 100%;
  .btn_list {
    position: absolute;
    right: 24px;
    z-index: 10;
  }
  .echarts {
    width: 100% !important;
    canvas {
      width: 90% !important;
    }
  }
}
</style>
