<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item :label="label">
        <el-select
          size="small"
          v-model="type"
          placeholder="请选择">
          <el-option
            v-for="item in list"
            :key="item.id"
            :label="item.name"
            :value="item.id"/>
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    label: {
      type: String,
      default: '学期：'
    },
    paramKey: {
      type: String,
      default: 'termid'
    },
    hasShortTerm: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      list: [
        { name: '春季班', id: '1' },
        { name: '春上', id: '11' },
        { name: '春下', id: '12' },
        { name: '暑假班', id: '2' },
        { name: '秋季班', id: '3' },
        { name: '秋上', id: '31' },
        { name: '秋下', id: '32' },
        { name: '寒假班', id: '4' }
      ],
      type: ''
    }
  },
  watch: {
    type (val) {
      let params = {}
      params[this.paramKey] = val
      this.$emit('getParams', params)
    },
    hasShortTerm (val) {
      this.addShortTerm()
    }
  },
  mounted () {
    this.type = '1'
    this.addShortTerm()
  },
  methods: {
    addShortTerm () {
      if (this.hasShortTerm) {
        let list = [
          { name: '短期班', id: '5' },
          { name: '活动类', id: '6' },
          { name: '考试类', id: 'ksl' },
          { name: '诊断类', id: 'zdl' }
        ]
        this.list = this.list.concat(list)
      }
    }
  }
}
</script>

