<script>
import Mixin from './mixin'
import SelectAll from '_c/dataReport/selectAll'
import { queryDateSection } from '@/api/user'
export default {
  name: 'KanbanRange',
  mixins: [ Mixin ],
  components: {
    SelectAll
  },
  data () {
    return {
      date: '',
      dateSection: []
    }
  },
  methods: {
    getDateSection () {
      queryDateSection().then(data => {
        this.dateSection = data
      }).catch(err => {
        this.$message.error('查询考勤周期切片错误：' + err.message)
      })
    },
    selectChange () {
      this.dateRange = this.date.split(' ~ ')
      this.queryUserList(this.dateRange)
    }
  },
  mounted () {
    this.performanceType = 2
    this.getDateSection()
  }
}
</script>

