import HttpRequest from 'axios'
import { runEnv } from './util'
const env = runEnv()
const Platform = 'SPLAT'
let baseURL = ''
switch (env) {
  case 'dev':
    baseURL = `/SPLAT`
    break
  case 'external':
    baseURL = `http://beta-gateway-one.speiyou.cn/${Platform}/`
    break
  case 'beta':
    baseURL = `http://gateway.one.speiyou.net/${Platform}/`
    // baseURL = `https://gateway-beta.one.speiyou.cn/${Platform}/`
    break
  case 'prod':
    baseURL = `https://gateway-one.speiyou.cn/${Platform}/`
    break
}
sessionStorage.setItem('currentEnv', env)
const Axios = HttpRequest.create({
  baseURL: baseURL,
  withCredentials: true, // 跨域请求时发送 cookies
  timeout: 30000, // request timeout
  headers: {
    platform: 'splat',
    'X-Ca-platform': 'service'
  },
  responseType: 'blob'
})
export default Axios
