<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item :label="label">
        <el-select
          size="small"
          v-model="type"
          placeholder="请选择">
          <el-option
            v-for="item in list"
            :key="item.value"
            :label="item.name"
            :value="item.value"/>
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    label: {
      type: String,
      default: '课程类型：'
    }
  },
  data () {
    return {
      list: [
        { name: '全部', value: '0' },
        { name: '面授+双师', value: '7' },
        { name: '在线', value: '1' }
      ],
      type: '0'
    }
  },
  watch: {
    type (val) {
      this.$emit('getParams', { serviceline: val })
    }
  },
  mounted () {
    this.$emit('getParams', { serviceline: this.type })
  },
  methods: {}
}
</script>

