import axios from '../libs/api.request'
/**
 *
 */
export function getUserPageList (params) {
  return axios.request({
    url: '/crmPanelList/getUserPageList',
    method: 'get',
    params
  })
}
/**
 * 数据看板-学员可用年级
 */
export function getCallGradeList (params) {
  return axios.request({
    url: '/crmPanelList/getCallGradeList',
    method: 'get',
    params
  })
}

/**
 * 数据看板列表
 */
export function list (params) {
  return axios.request({
    url: '/crmPanelList/list',
    method: 'post',
    data: params
  })
}

