<template>
  <el-form
    label-width="120px"
    size="medium">
    <el-form-item label="截止日期：">
      <el-date-picker
        size="small"
        v-model="time"
        :picker-options="pickerTime"
        :clearable="false"
        type="date"
        value-format="yyyy-MM-dd"
        style="width: 400px"
        placeholder="选择日期"/>
    </el-form-item>
  </el-form>
</template>
<script>
import util from '@/common/util'
const oneDay = 24 * 60 * 60 * 1000

export default {
  data () {
    return {
      time: '',
      pickerTime: {
        disabledDate (time) {
          return time.getTime() > new Date(new Date().getTime() - oneDay)
        }
      }
    }
  },
  watch: {
    time (val) {
      let deadline = ''
      // 如果返回val 为null  直接返回空值
      if (val) {
        deadline = val + ' 23:59:59'
      }
      this.$emit('getParams', { deadline })
    }
  },
  mounted () {
    this.typeDay()
  },
  methods: {
    // 按天 时间默认为前一天
    typeDay () {
      const day = util.getDayTime(1)
      this.time = day.FullDate
    }
  }
}
</script>

