<template>
  <!-- 任务看板 -->
  <Layout class="content-data">
    <div class="content-data-card">
      <select-time @getParams="getParams"/>
      <select-area
        :has-time="true"
        :start-time="params.starttime"
        :end-time="params.endtime"
        @getInitList="search()"
        @getParams="getParams" />
      <div class="exprot">
        <el-button
          :disabled="loading"
          size="small"
          type="primary"
          @click="searchButton()">查询</el-button>
        <el-button
          size="small"
          type="primary"
          @click="exprot">导出数据</el-button>
      </div>
      <div class="table-basic">
        <el-table
          v-loading="loading"
          :data="tableData"
          style="width: 100%">
          <el-table-column
            type="index"
            label="序号"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="queryDate"
            label="时间"
            min-width="150"
            align="center"/>
          <el-table-column
            prop="cityName"
            label="城市"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="areaName"
            label="区域"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="servicecenterName"
            label="服务中心"
            min-width="150"
            align="center"/>
          <el-table-column
            prop="userName"
            label="家庭顾问"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="newProduceTaskNum"
            label="新产生任务条数"
            min-width="160"
            align="center"/>
          <el-table-column
            prop="newProduceTaskPerson"
            label="新产生任务人数"
            min-width="140"
            align="center"/>
          <el-table-column
            prop="newProduceFollowNum"
            label="新任务维护条数"
            min-width="140"
            align="center"/>
          <el-table-column
            prop="newProduceFollowPerson"
            label="新任务维护人数"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="newProduceFollowRatio"
            label="新任务维护率"
            min-width="110"
            align="center"/>
          <el-table-column
            prop="statNewProduceFollowNum"
            label="统计时间范围内维护任务的条数"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="statNewProducePerson"
            label="统计时间范围内维护任务的人数"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="avgResponseTime"
            label="任务平均响应时长"
            min-width="100"
            align="center"/>
        </el-table>
      </div>
      <el-pagination
        :current-page="params.page"
        :total="total"
        :page-size="params.pagesize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"/>
    </div>
  </Layout>
</template>
<script>
import util from '@/common/util'
import selectArea from '@/components/dataReport/selectArea'
import selectTime from '@/components/dataReport/selectTime'
import verify from './verify.js'
import { list as getList } from '@/api/crmPanel'

import config from '../../config'
import { runEnv } from '../../libs/util'
const env = runEnv()
const urls = {
  // getList: '/xinxin/i/panel/list?classify=task',
  // exportUrl: '/xinxin/i/panel/download?classify=task',
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/download'
  // exportUrl: 'https://gateway-one.speiyou.cn/SPLAT/crmPanelList/download'
}

export default {
  name: 'dataReportTask',
  components: {
    selectArea,
    selectTime
  },
  data () {
    return {
      total: 0,
      params: {
        starttime: '',
        endtime: '',
        cities: '',
        areas: '',
        servcenters: '',
        collect: '',
        counttime: '',
        users: '',
        page: 1,
        pagesize: 10
      },
      historyParams: {},
      tableData: [],
      loading: false
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.params.page = val
      this.historyParams.page = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pagesize = val
      this.historyParams.pagesize = val
      this.search(this.historyParams)
    },
    searchButton () {
      this.params.page = 1
      this.search()
    },
    async search (historyParams) {
      let params = historyParams || { ...this.params }
      let val = verify(params)
      if (!val.check) {
        this.$message({
          message: val.msg,
          type: 'warning'
        })
        return
      }

      params.operate = 'task'
      this.historyParams = params
      // let { data } = await this.$axios.post(urls.getList, params)
      // this.tableData = data.records || []
      // this.total = data.totalCount
      try {
        this.loading = true
        const data = await getList(params)
        this.tableData = data.records || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    getParams (p) {
      let params = {
        ...this.params,
        ...p
      }
      this.params = params
    },
    exprot () {
      util.download(urls.exportUrl, this.historyParams)
    }
  }
}
</script>

