<template>
  <div class="detail-wrapper">
    <FunnelData :funnelData="detailData"
                :key-map="keyMap.funnelItems" >
      <template slot="title">
        <div class="title-wrapper">
          <span class="title1">{{ keyMap.name }}</span>
          <Tooltip transfer :content="keyMap.tooltip" max-width="200">
            <Icon type="ios-information-circle-outline" :size="16"/>
          </Tooltip>
        </div>
        <div class="target-wrapper">
          <div class="target-item">
            <Tooltip transfer :content="keyMap.items[0].tooltip" max-width="200">
              <span class="item-name">{{ keyMap.items[0].name }}</span>
              <span class="item-num">{{ detailData[keyMap.items[0].key] | toThousands }}</span>
            </Tooltip>
          </div>
          <div class="target-item" v-show="userInfo.crmRoleId !== 2">
            <Tooltip transfer :content="keyMap.items[1].tooltip" max-width="200">
              <span class="item-name">{{ keyMap.items[1].name }}</span>
              <span class="item-num">{{ detailData[keyMap.items[1].key] | toThousands }}</span>
            </Tooltip>
          </div>
        </div>
      </template>
      <div slot="chart" class="chart-wrapper">
        <ChartItem v-for="chart in keyMap.charts"
                   :key="chart.key"
                   :name="chart.name"
                   :num="detailData[chart.key] || 0"
                   style="margin-bottom: 7px"
                   :max="max"
                   :no-data="chart.noData"></ChartItem>
      </div>
    </FunnelData>
  </div>
</template>
<script>
import ChartItem from './chart-item'
import FunnelData from './funnel-data'
export default {
  name: 'ChartDetail',
  props: {
    detailData: Object, // 数据
    keyMap: Object // 字段定义
  },
  components: {
    ChartItem,
    FunnelData
  },
  data () {
    return {
    }
  },
  computed: {
    max () {
      return Math.max(...this.keyMap.charts.map(chart => this.detailData[chart.key] || 0))
    },
    userInfo () {
      return this.$store.state.roleRelativeData.userInfo
    }
  }
}
</script>

