<template>
  <!-- 学员满意度评价 -->
  <Layout class="content-data">
    <div class="content-data-card">
      <select-time
        :init-params="storageParams"
        @getParams="getParams"/>
      <select-area
        :has-time="true"
        :start-time="params.starttime"
        :end-time="params.endtime"
        :init-params="storageParams"
        @getInitList="search()"
        @getParams="getParams"/>
      <div class="exprot">
        <el-button
          :disabled="loading"
          size="small"
          type="primary"
          @click="searchButton()">查询</el-button>
        <el-button
          size="small"
          type="primary"
          @click="exprot">导出数据</el-button>
      </div>
      <div class="table-basic">
        <el-table
          v-loading="loading"
          :data="tableData"
          style="width: 100%">
          <el-table-column
            type="index"
            label="序号"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="queryDate"
            label="时间"
            min-width="150"
            align="center"/>
          <el-table-column
            prop="cityName"
            label="城市"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="areaName"
            label="区域"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="servicecenterName"
            label="服务中心"
            min-width="150"
            align="center"/>
          <el-table-column
            prop="userName"
            label="家庭顾问"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="bindedMaintainStudentNum"
            label="已绑定维护中学员人数"
            min-width="160"
            align="center"/>
          <el-table-column
            prop="evaluateStudentNumber"
            label="实际评价学员人数"
            min-width="140"
            align="center"/>
          <el-table-column
            prop="evaluateNumber"
            label="实际评价条数"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="satisfacationScore"
            label="满意度分数"
            min-width="110"
            align="center"/>
          <el-table-column
            prop="evaluateRatio"
            label="评价率"
            min-width="100"
            align="center"/>
          <el-table-column
            v-if="storageParams.collect ==='4' || storageParams.collect ==='5'"
            prop="id8"
            fixed="right"
            label="查看评价明细"
            min-width="120"
            align="center">
            <template slot-scope="scope">
              <el-button
                type="text"
                @click="look(scope.row)">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination
        :current-page="params.page"
        :total="total"
        :page-size="params.pagesize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"/>
    </div>
  </Layout>
</template>
<script>
import verify from '../verify.js'
import util from '@/common/util'
import selectArea from '@/components/dataReport/selectArea'
import selectTime from '@/components/dataReport/selectTime'
// import { setTimeout } from 'timers'
import {
  list as getList
} from '@/api/crmPanel'

import config from '../../../config'
import { runEnv } from '../../../libs/util'
const env = runEnv()
const urls = {
  // getList: '/xinxin/i/panel/list?classify=evaluation',
  // exportUrl: '/xinxin/i/panel/download?classify=evaluation',
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/download'
  // exportUrl: 'http://10.4.4.206:8667/crmPanelList/download'
}
export default {
  name: 'dataReportSatisfaction',
  components: {
    selectArea,
    selectTime
  },
  data () {
    return {
      total: 0,
      exportUrl: urls.exportUrl,
      params: {
        starttime: '',
        endtime: '',
        cities: '',
        areas: '',
        servcenters: '',
        collect: '',
        counttime: '',
        users: '',
        page: 1,
        pagesize: 10
      },
      historyParams: {},
      tableData: [],
      loading: false
    }
  },
  computed: {
    storageParams () {
      return this.$store.state.data.satisfactionParams || {}
    }
  },
  mounted () {
    this.params = {
      ...this.params,
      ...this.storageParams
    }
  },
  destroyed () {
    let params = {
      cities: '',
      areas: '',
      servcenters: '',
      users: ''
    }
    this.$store.commit('data/setSatisfactionParams', params)
  },
  methods: {
    handleCurrentChange (val) {
      this.params.page = val
      this.historyParams.page = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pagesize = val
      this.historyParams.pagesize = val
      this.search(this.historyParams)
    },
    look (row) {
      this.$router.push({
        name: 'dataReportSatisfactionEvaluation',
        query: {
          id: row.userId
        }
      })
    },
    searchButton () {
      this.params.page = 1
      this.search()
    },
    async search (historyParams) {
      const params = historyParams || { ...this.params }
      let val = verify(params)
      if (!val.check) {
        this.$message({
          message: val.msg,
          type: 'warning'
        })
        return
      }
      params.operate = 'evaluation'
      this.historyParams = params
      this.$store.commit('data/setSatisfactionParams', params)
      // let { data } = await this.$axios.post(urls.getList, params)
      // this.tableData = data.records || []
      // this.total = data.totalCount
      try {
        this.loading = true
        const data = await getList(params)
        this.tableData = data.records || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    async exprot () {
      this.historyParams['empNo'] = '075757'
      util.download(urls.exportUrl, this.historyParams)
      // try {
      //   this.historyParams['isForm'] = true
      //   const data = await download(this.historyParams)
      //   console.log(data)
      //   debugger
      // } catch (err) {
      //   this.$Message.error({
      //     content: '导出失败：' + err.message
      //   })
      // }
    },
    getParams (p) {
      let params = {
        ...this.params,
        ...p
      }
      this.params = params
    }
  }
}
</script>

