<template>
  <!-- 在读学员续报、扩科 -->
  <Layout class="content-data">
    <div class="content-data-card">
      <!-- <select-time
        :type-list="timeList"
        @getParams="getParams"/> -->
      <select-area
        :has-time="false"
        @getInitList="search()"
        @getParams="getParams"/>
      <el-form
        label-width="120px"
        size="medium">
        <el-form-item label="统计项：">
          <el-radio
            v-model="params.stattype"
            label="1">续报</el-radio>
          <el-radio
            v-model="params.stattype"
            label="2">扩科</el-radio>
        </el-form-item>
      </el-form>
      <class-type
        label="在读课程类型："
        @getParams="getParams"/>
      <select-year
        label="在读年份："
        param-key="inyear"
        @getParams="getParams"/>
      <class-term
        label="在读学期："
        param-key="interm"
        @getParams="getParams"/>
      <class-subject
        label="在读学科："
        param-key="insubject"
        @getParams="getParams"/>
      <div v-if="params.stattype === '1'">
        <select-year
          label="续报年份："
          param-key="continueyear"
          @getParams="getParams"/>
        <class-term
          label="续报学期："
          param-key="continueterm"
          @getParams="getParams"/>
      </div>
      <div v-if="params.stattype === '2'">
        <select-year
          label="扩科年份："
          param-key="continueyear"
          @getParams="getParams"/>
        <class-term
          label="扩科学期："
          param-key="continueterm"
          @getParams="getParams"/>
        <class-subject
          label="扩科学科："
          param-key="continuesubid"
          @getParams="getParams"/>
      </div>
      <div class="exprot">
        <el-button
          :disabled="loading"
          size="small"
          type="primary"
          @click="searchButton()">查询</el-button>
        <el-button
          size="small"
          type="primary"
          @click="exprot">导出数据</el-button>
      </div>
      <div class="table-basic">
        <el-table
          v-loading="loading"
          :data="tableData"
          style="width: 100%">
          <el-table-column
            label="序号"
            type="index"
            min-width="60"
            align="center"/>
          <el-table-column
            prop="cityName"
            label="城市"
            min-width="80"
            align="center"/>
          <el-table-column
            prop="areaName"
            label="区域"
            min-width="120"
            align="center"/>
          <el-table-column
            prop="servicecenterName"
            label="服务中心"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="userName"
            label="家庭顾问"
            min-width="100"
            align="center"/>
          <el-table-column
            prop="inNum"
            label="在读学员人数"
            min-width="120"
            align="center"/>searchStattype
          <el-table-column
            prop="inManTime"
            label="在读学员人次"
            min-width="100"
            align="center"/>
          <el-table-column
            v-if="searchStattype === '1'"
            prop="continueNum"
            label="续报学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            v-if="searchStattype === '1'"
            prop="continueManTime"
            label="续报学员人次"
            min-width="100"
            align="center"/>
          <el-table-column
            v-if="searchStattype === '1'"
            prop="continueRate"
            label="续报率"
            min-width="100"
            align="center"/>
          <el-table-column
            v-if="searchStattype === '2'"
            prop="continueNum"
            label="扩科学员人数"
            min-width="100"
            align="center"/>
          <el-table-column
            v-if="searchStattype === '2'"
            prop="continueManTime"
            label="扩科学员人次"
            min-width="100"
            align="center"/>
          <el-table-column
            v-if="searchStattype === '2'"
            prop="continueRate"
            label="扩科率"
            min-width="100"
            align="center"/>
        </el-table>
      </div>
      <el-pagination
        :current-page="params.page"
        :total="total"
        :page-size="params.pagesize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"/>
    </div>
  </Layout>
</template>
<script>
import selectArea from '@/components/dataReport/selectArea'
import selectTime from '@/components/dataReport/selectTime'
import selectYear from '@/components/dataReport/selectYear'
import classType from '@/components/dataReport/classType'
import classTerm from '@/components/dataReport/classTerm'
import classSubject from '@/components/dataReport/classSubject'
import util from '@/common/util'
import verify from './verify.js'

import { list as getList } from '@/api/crmPanel'

import config from '../../config'
import { runEnv } from '../../libs/util'
const env = runEnv()
const urls = {
  // getList: '/xinxin/i/panel/list?classify=resubmit',
  // exportUrl: '/xinxin/i/panel/download?classify=resubmit',
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/download'
  // exportUrl: 'https://gateway-one.speiyou.cn/SPLAT/crmPanelList/download'
}

export default {
  name: 'dataReportKeep',
  components: {
    selectArea,
    selectTime,
    selectYear,
    classType,
    classTerm,
    classSubject
  },
  data () {
    return {
      total: 0,
      timeList: ['1', '2', '3', '5', '6'],
      params: {
        cities: '',
        areas: '',
        servcenters: '',
        collect: '',
        users: '',
        stattype: '1',
        serviceline: '0',
        inyear: '',
        interm: '',
        insubject: '',
        continueyear: '',
        continueterm: '',
        continuesubid: '',
        page: 1,
        pagesize: 10
      },
      historyParams: {},
      tableData: [],
      loading: false,
      value1: '',
      searchStattype: '1'
    }
  },
  methods: {
    handleCurrentChange (val) {
      this.params.page = val
      this.historyParams.page = val
      this.search(this.historyParams)
    },
    handleSizeChange (val) {
      this.params.page = 1
      this.historyParams.page = 1
      this.params.pagesize = val
      this.historyParams.pagesize = val
      this.search(this.historyParams)
    },
    getParams (p) {
      let params = {
        ...this.params,
        ...p
      }
      this.params = params
    },
    searchButton () {
      this.params.page = 1
      this.search()
    },
    async search (historyParams) {
      let params = historyParams || { ...this.params }
      if (params.stattype === '1') {
        delete params.continuesubid
      }
      let val = verify(params)
      if (!val.check) {
        this.$message({
          message: val.msg,
          type: 'warning'
        })
        return
      }
      params.operate = 'resubmit'
      this.historyParams = params
      // params.operate = 'resubmit'
      // const data = await getList(params)
      // let { data } = await this.$axios.post(urls.getList, params)
      // this.searchStattype = this.params.stattype
      // this.tableData = data.records || []
      // this.total = data.totalCount
      try {
        this.loading = true
        const data = await getList(params)
        this.searchStattype = this.params.stattype
        this.tableData = data.records || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    exprot () {
      util.download(urls.exportUrl, this.historyParams)
    }
  }
}
</script>

