<template>
  <el-select
    size="small"
    :clearable="clearable"
    :placeholder="placeholder"
    :value="select"
    :disabled="pageDisabled"
    :filterable="filterable"
    multiple
    @change="handelChange"
    style="width: 400px">
    <el-option
      v-for="item in list"
      :key="item[valueKey]"
      :label="item[labelKey]"
      :value="item[valueKey]"/>
  </el-select>
</template>
<script>
export default {
  props: {
    placeholder: {
      type: String,
      default: '请选择'
    },
    clearable: {
      type: Boolean,
      default: true
    },
    dataList: {
      type: Array,
      default: function () {
        return []
      }
    },
    valueKey: {
      type: String,
      default: 'value'
    },
    labelKey: {
      type: String,
      default: 'label'
    },
    allValue: {
      type: String,
      default: 'all'
    },
    allKey: {
      type: String,
      default: '全部'
    },
    initSelect: {
      type: Array,
      default: function () {
        return []
      }
    },
    disabled: {
      type: Boolean,
      default: false
    },
    filterable: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      list: [],
      select: [],
      oldAllSelect: [],
      pageDisabled: false
    }
  },
  computed: {},
  watch: {
    dataList (val) {
      this.list = this.allList(val)
    },
    initSelect (val) {
      this.select = this.allListValue(val)
      this.oldAllSelect[0] = this.select
    },
    disabled () {
      this.pageDisabled = this.disabled
    }
  },
  mounted () {
    this.list = this.dataList
    this.select = this.initSelect
    this.pageDisabled = this.disabled
  },
  methods: {
    handelChange (val) {
      // 取全部数据的value
      this.select = val
      let allList = []

      for (let i = 0; i < this.list.length; i++) {
        allList.push(this.list[i][this.valueKey])
      }
      // 取上次的数据
      const oldVal = this.oldAllSelect.length === 1 ? this.oldAllSelect[0] : []
      // 包含全选 全选
      if (val.includes(this.allValue)) {
        this.select = allList
      }
      // 上次的数据包含全选，这次不包括 是取消全选
      if (oldVal.includes(this.allValue) && !val.includes(this.allValue)) {
        this.select = []
      }
      // 新老数据都有全部选中
      if (oldVal.includes(this.allValue) && val.includes(this.allValue)) {
        const index = val.indexOf(this.allValue)
        val.splice(index, 1) // 排除全选选项
        this.select = val
      }
      // 全选未选 但是其他选项全部选上 则全选选上 上次和当前 都没有全选
      if (!oldVal.includes(this.allValue) && !val.includes(this.allValue)) {
        if (val.length === allList.length - 1) {
          this.select = [this.allValue].concat(val)
        }
      }
      this.oldAllSelect[0] = this.select
      const inputValue = this.removeAll(this.select)
      this.$emit('input', inputValue)
      this.$emit('change', inputValue)
    },
    allList (val) {
      let list = typeof val === 'object' ? [...val] : []
      const { labelKey, allKey, valueKey, allValue } = this
      if (list.length > 1) {
        let all = {}
        all[labelKey] = allKey
        all[valueKey] = allValue
        list = [all].concat(list)
      }
      return list
    },
    allListValue (val) {
      const { dataList, initSelect, allValue } = this
      let list = typeof val === 'object' ? [...val] : []
      if (dataList.length > 1 && dataList.length === initSelect.length) {
        list = [allValue].concat(list)
      }
      return list
    },
    // 去掉all
    removeAll (val) {
      let list = [...val]
      if (list.includes(this.allValue)) {
        let i = list.indexOf(this.allValue)
        list.splice(i, 1)
      }
      return list
    }
  }
}
</script>

<style lang="less">
.el-select__tags {
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 100px;
}
</style>
